/**
 * Media Library Main Component
 * Orchestrates all media library functionality using custom hooks
 *
 * Uses server-side pagination, filtering, and search via /ai-media/v1/library endpoint
 */

import { useState, useEffect, useMemo } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

// Custom hooks
import { useAttachments } from '../../hooks/useAttachments';
import { useFilters } from '../../hooks/useFilters';
import { usePagination } from '../../hooks/usePagination';
import { useSelection } from '../../hooks/useSelection';
import { useQueueStatus } from '../../hooks/useQueueStatus';
import { useEditableField, useApprove } from '../../hooks/useEditableField';
import { useCostTracking } from '../../hooks/useCostTracking';
import { useColumnVisibility } from '../../hooks/useColumnVisibility';
import { useUpload } from '../../hooks/useUpload';
import { useBatchProcessing } from '../../hooks/useBatchProcessing';
import { useAttachModal } from '../../hooks/useAttachModal';
import { useThumbnailPreview } from '../../hooks/useThumbnailPreview';

// Utility functions
import { isAttachmentProcessing } from '../../utils/attachmentHelpers';
import {
	STATUS_FILTER_OPTIONS,
	ATTACHMENT_FILTER_OPTIONS,
} from '../../utils/constants';

// Shared components
import AdminNotice from '../shared/AdminNotice';
import ThumbnailPreview from '../shared/ThumbnailPreview';
import LoadingScreen from '../shared/LoadingScreen';

// Modal components
import BatchProcessingModal from '../modals/BatchProcessingModal';
import AttachModal from '../modals/AttachModal';
import UploadModal from '../modals/UploadModal';
import CostStatsModal from '../modals/CostStatsModal';
import SettingsModal from '../modals/SettingsModal';
import SpeedModeSetupModal from '../modals/SpeedModeSetupModal';

// Media library components
import MediaLibraryHeader from './MediaLibraryHeader';
import MediaLibraryToolbar from './MediaLibraryToolbar';
import BulkActions from './BulkActions';
import MediaLibraryTable from './MediaLibraryTable';

const MediaLibrary = () => {
	// ===== CUSTOM HOOKS =====

	// Data management with server-side pagination
	const attachmentsData = useAttachments();

	// Filters (server-side filtering)
	const filtersState = useFilters();

	// Pagination (server-side pagination)
	const paginationState = usePagination();

	// Callback for refreshing library (used by upload and bulk delete)
	const refreshLibrary = () => {
		attachmentsData.invalidateCache();
		paginationState.setCurrentPage(1); // Reset to page 1
		// Force reload with current filters
		setTimeout(() => {
			const currentFilters = {
				status: filtersState.statusFilter,
				attachmentFilter: filtersState.attachmentFilter,
				search: filtersState.searchQuery,
				skip_count: false, // FORCE COUNT immediately (not progressive) after upload/delete
			};
			attachmentsData.loadLibrary(1, paginationState.perPage, currentFilters);
		}, 100);
	};

	// Other hooks
	const selection = useSelection(refreshLibrary);
	const queueStatus = useQueueStatus(attachmentsData.items, attachmentsData.refreshAttachment);
	const costs = useCostTracking();
	const columnVisibility = useColumnVisibility();

	// Upload with callback to refresh library after upload
	const handleUploadComplete = () => {
		refreshLibrary();
	};

	const upload = useUpload(handleUploadComplete);

	// Local state
	const [adminNotice, setAdminNotice] = useState(null);
	const [showSettingsModal, setShowSettingsModal] = useState(false);
	const [showSpeedModeModal, setShowSpeedModeModal] = useState(false);
	const [pendingBatchMode, setPendingBatchMode] = useState(null);
	const [pendingImageCount, setPendingImageCount] = useState(0);

	// Build filters object for API
	// NOTE: skip_count defaults to TRUE in useAttachments (progressive loading enabled by default)
	const filters = useMemo(() => ({
		status: filtersState.statusFilter,
		attachmentFilter: filtersState.attachmentFilter,
		search: filtersState.searchQuery,
		// skip_count is NOT set here - useAttachments will default to TRUE (progressive mode)
		// unless search is active (then it forces FALSE for immediate count)
	}), [filtersState.statusFilter, filtersState.attachmentFilter, filtersState.searchQuery]);

	// Load data when filters or page changes
	useEffect(() => {
		attachmentsData.loadLibrary(paginationState.currentPage, paginationState.perPage, filters);
	}, [paginationState.currentPage, paginationState.perPage, filters, attachmentsData.loadLibrary]);

	//  OPTIMLN: Pre-fetch tier detection on mount (for faster Speed Mode modal)
	useEffect(() => {
		// Pre-fetch tier info in background to populate cache
		// This runs once when MediaLibrary mounts
		apiFetch({
			path: '/ai-media/v1/settings/detect-tiers',
			method: 'POST',
			data: {
				force_refresh: false, // Use cache if available
			},
		}).catch(() => {
			// Silently ignore errors - this is just pre-caching
			// If it fails, SpeedModeSelector will fetch it when needed
		});
	}, []); // Empty deps = run once on mount

	// Handle filter changes (invalidate cache and reset page)
	const handleStatusFilterChange = (newStatus) => {
		attachmentsData.invalidateCache();
		filtersState.setStatusFilter(newStatus);
		paginationState.resetPage();
	};

	const handleAttachmentFilterChange = (newFilter) => {
		attachmentsData.invalidateCache();
		filtersState.setAttachmentFilter(newFilter);
		paginationState.resetPage();
	};

	const handleSearch = () => {
		attachmentsData.invalidateCache();
		filtersState.setSearchQuery(filtersState.searchInput);
		paginationState.resetPage();
	};

	const handleSearchKeyDown = (e) => {
		if (e.key === 'Enter') {
			handleSearch();
		}
	};

	const handleClearSearch = () => {
		attachmentsData.invalidateCache();
		filtersState.setSearchInput('');
		filtersState.setSearchQuery('');
		paginationState.resetPage();
	};

	// Speed Mode Setup Handlers
	const handleBatchAnalyzeClick = async (mode) => {
		//  QUICK WIN 1: Show modal IMMEDIATELY for instant feedback
		setPendingBatchMode(mode);
		setPendingImageCount(0); // Will be updated when API responds
		setShowSpeedModeModal(true);

		// Fetch how many images will be processed in background
		try {
			const batchResponse = await apiFetch({
				path: '/ai-media/v1/batch-analyze',
				method: 'POST',
				data: {
					mode: mode,
					attachment_ids: mode === 'selected' ? selection.selectedIds : [],
				},
			});

			if (batchResponse.success && batchResponse.attachment_ids.length > 0) {
				// Update image count (modal is already visible)
				setPendingImageCount(batchResponse.attachment_ids.length);
			} else {
				// No images to process - close modal and show notice
				setShowSpeedModeModal(false);
				setPendingBatchMode(null);
				setAdminNotice({
					type: 'warning',
					message: batchResponse.message || 'No images found to process.',
				});
			}
		} catch (error) {
			// Error - close modal and show notice
			setShowSpeedModeModal(false);
			setPendingBatchMode(null);
			setAdminNotice({
				type: 'error',
				message: error.message || 'Failed to prepare batch processing.',
			});
		}
	};

	const handleSpeedModeConfirm = (speedMode, concurrency) => {
		// Close modal
		setShowSpeedModeModal(false);

		// Start batch processing with selected speed mode
		if (pendingBatchMode) {
			batchProcessing.handleBatchAnalyze(pendingBatchMode, speedMode, concurrency);
		}

		// Reset pending state
		setPendingBatchMode(null);
		setPendingImageCount(0);
	};

	const handleSpeedModeCancel = () => {
		setShowSpeedModeModal(false);
		setPendingBatchMode(null);
		setPendingImageCount(0);
	};

	// Clear OPCache
	const handleClearOpcache = async () => {
		try {
			const data = await apiFetch({
				path: '/ai-media/v1/clear-opcache',
				method: 'POST',
			});

			// Show WordPress admin notice
			setAdminNotice({
				type: data.success ? 'success' : 'warning',
				message: data.message,
			});

			// If successful, refresh page after showing message
			if (data.success) {
				setTimeout(() => {
					window.location.reload();
				}, 1500); // 1.5 seconds to read the message
			} else {
				// Auto-hide notice after 5 seconds if not successful
				setTimeout(() => {
					setAdminNotice(null);
				}, 5000);
			}
		} catch (error) {
			setAdminNotice({
				type: 'error',
				message: 'Failed to clear OPCache: ' + error.message,
			});

			setTimeout(() => {
				setAdminNotice(null);
			}, 5000);
		}
	};

	// Helper function for checking if attachment is processing
	const isProcessing = (attachment) => isAttachmentProcessing(attachment, queueStatus.queueStatus);

	// Batch processing
	const batchProcessing = useBatchProcessing({
		addProcessingId: queueStatus.addProcessingId,
		removeProcessingId: queueStatus.removeProcessingId,
		refreshAttachment: attachmentsData.refreshAttachment,
		addCost: costs.addCost,
		processingIds: queueStatus.processingIds,
		setAllAttachments: attachmentsData.setItems,
		selectedIds: selection.selectedIds,
		clearSelection: selection.clearSelection,
		setAdminNotice: setAdminNotice,
	});

	// Editable fields with type-specific handlers
	const altField = useEditableField('alt', {
		refreshAttachment: attachmentsData.refreshAttachment,
		isAttachmentProcessing: isProcessing,
	});

	const captionField = useEditableField('caption', {
		refreshAttachment: attachmentsData.refreshAttachment,
		isAttachmentProcessing: isProcessing,
	});

	const titleField = useEditableField('title', {
		refreshAttachment: attachmentsData.refreshAttachment,
		isAttachmentProcessing: isProcessing,
	});

	const statusField = useEditableField('status', {
		refreshAttachment: attachmentsData.refreshAttachment,
		isAttachmentProcessing: isProcessing,
	});

	// Approve handler
	const { handleApprove } = useApprove({
		refreshAttachment: attachmentsData.refreshAttachment,
		isAttachmentProcessing: isProcessing,
	});

	// Attach modal
	const attachModal = useAttachModal(attachmentsData.attachToParent);

	// Thumbnail preview
	const thumbnailPreview = useThumbnailPreview();

	// ===== RENDER =====

	// Show loading state
	if (attachmentsData.loading) {
		return (
			<div className="wrap ai-media-library">
				<MediaLibraryHeader onAddImage={upload.openUploadModal} />
				<LoadingScreen
					message="Loading your media library"
					showTips={true}
				/>
			</div>
		);
	}

	// Show error state
	if (attachmentsData.error) {
		return (
			<div className="ai-media-library">
				<div className="notice notice-error">
					<p>Error loading attachments: {attachmentsData.error}</p>
				</div>
			</div>
		);
	}

	return (
		<div className="wrap ai-media-library">
			<AdminNotice notice={adminNotice} onDismiss={() => setAdminNotice(null)} />

			<MediaLibraryHeader onAddImage={upload.openUploadModal} />

			<MediaLibraryToolbar
				searchInput={filtersState.searchInput}
				onSearchChange={filtersState.setSearchInput}
				onSearch={handleSearch}
				onSearchKeyDown={handleSearchKeyDown}
				onClearSearch={handleClearSearch}
				statusFilter={filtersState.statusFilter}
				onStatusFilterChange={handleStatusFilterChange}
				statusFilterOptions={STATUS_FILTER_OPTIONS}
				attachmentFilter={filtersState.attachmentFilter}
				onAttachmentFilterChange={handleAttachmentFilterChange}
				attachmentFilterOptions={ATTACHMENT_FILTER_OPTIONS}
			/>

			<BulkActions
				selectedIds={selection.selectedIds}
				batchProcessing={batchProcessing.batchProcessing}
				onBatchAnalyze={handleBatchAnalyzeClick}
				onBulkDelete={selection.handleBulkDelete}
				onBulkStatusChange={selection.handleBulkStatusChange}
				totalCost={costs.totalCost}
				loadingCosts={costs.loadingCosts}
				onShowCostModal={() => {
					costs.setShowCostModal(true);
					costs.loadTotalCost(); // Refresh total cost from database
				}}
				onLoadCostStats={() => {
					costs.loadCostStats();
					costs.loadTotalCost(); // Also refresh total
				}}
				onShowSettingsModal={() => setShowSettingsModal(true)}
				onClearOpcache={handleClearOpcache}
			/>

			<MediaLibraryTable
				paginatedAttachments={attachmentsData.items}
				selectedIds={selection.selectedIds}
				onSelectAll={() => selection.selectAll(attachmentsData.items)}
				onToggleSelection={selection.toggleSelection}
				isColumnVisible={columnVisibility.isColumnVisible}
				currentPage={paginationState.currentPage}
				totalPages={attachmentsData.totalPages}
				totalItems={attachmentsData.totalItems}
				countLoading={attachmentsData.countLoading}
				onPageChange={paginationState.goToPage}
				editingTitle={titleField.editing}
				savingTitle={titleField.saving}
				editingAlt={altField.editing}
				savingAlt={altField.saving}
				editingCaption={captionField.editing}
				savingCaption={captionField.saving}
				editingStatus={statusField.editing}
				savingStatus={statusField.saving}
				queueStatus={queueStatus.queueStatus}
				processingIds={queueStatus.processingIds}
				onTitleClick={titleField.handleClick}
				onTitleChange={titleField.updateValue}
				onTitleKeyDown={titleField.handleKeyDown}
				onTitleSave={titleField.handleSave}
				onLoadAllAttachments={refreshLibrary}
				onAltClick={altField.handleClick}
				onAltChange={altField.updateValue}
				onAltKeyDown={altField.handleKeyDown}
				onAltSave={altField.handleSave}
				onCaptionClick={captionField.handleClick}
				onCaptionChange={captionField.updateValue}
				onCaptionKeyDown={captionField.handleKeyDown}
				onCaptionSave={captionField.handleSave}
				onStatusClick={statusField.handleClick}
				onStatusChange={statusField.updateValue}
				onStatusSave={statusField.handleSave}
				onApprove={handleApprove}
				onAttachToPost={attachModal.handleOpen}
				onDetach={attachmentsData.handleDetach}
				onAnalyze={batchProcessing.handleAnalyze}
				onThumbnailEnter={thumbnailPreview.handleThumbnailEnter}
				onThumbnailLeave={thumbnailPreview.handleThumbnailLeave}
			/>

			<ThumbnailPreview hoveredImage={thumbnailPreview.hoveredImage} />

			<BatchProcessingModal
				batchProgress={batchProcessing.batchProgress}
				processingStatus={batchProcessing.processingStatus}
				onCancel={() => {
				// Set cancel flag - feedback is shown in modal
				batchProcessing.cancelledRef.current = true;
			}}
				onClose={() => {
					// Handled by the hook internally
				}}
				cancelledRef={batchProcessing.cancelledRef}
			/>

			<AttachModal
				isOpen={attachModal.isOpen}
				onClose={attachModal.handleClose}
				search={attachModal.search}
				onSearchChange={attachModal.setSearch}
				onSearch={attachModal.handleSearch}
				onClearSearch={attachModal.handleClearSearch}
				searching={attachModal.searching}
				results={attachModal.results}
				selected={attachModal.selected}
				onSelect={attachModal.setSelected}
				onConfirm={attachModal.handleConfirm}
			/>

			<UploadModal
				isOpen={upload.uploadModalOpen}
				onClose={upload.closeUploadModal}
				uploadFiles={upload.uploadFiles}
				uploadProgress={upload.uploadProgress}
				uploading={upload.uploading}
				onFileSelect={upload.handleFileSelect}
				onStartUpload={upload.handleStartUpload}
				onRemoveFile={upload.handleRemoveFile}
				fileInputRef={upload.fileInputRef}
				onDragEnter={upload.handleDragEnter}
				onDragLeave={upload.handleDragLeave}
				onDragOver={upload.handleDragOver}
				onDrop={upload.handleDrop}
				isDragging={upload.isDragging}
			/>

			<CostStatsModal
				isOpen={costs.showCostModal}
				onClose={() => costs.setShowCostModal(false)}
				costStats={costs.costStats}
				costBreakdown={costs.costBreakdown}
				liveTotalCost={costs.totalCost}
			/>

			<SettingsModal
				isOpen={showSettingsModal}
				onClose={() => setShowSettingsModal(false)}
				preselectedImageId={selection.selectedIds.length > 0 ? selection.selectedIds[0] : null}
			/>

			<SpeedModeSetupModal
				isOpen={showSpeedModeModal}
				imageCount={pendingImageCount}
				onConfirm={handleSpeedModeConfirm}
				onCancel={handleSpeedModeCancel}
			/>
		</div>
	);
};

export default MediaLibrary;
