/**
 * Settings Modal Component
 * Full-screen modal for managing plugin settings from the Media Library
 */

import { useState, useEffect } from '@wordpress/element';
import { Button, Modal } from '@wordpress/components';
import apiFetch from '@wordpress/api-fetch';
import AdminNotice from '../shared/AdminNotice';

// Import tabs (SAME as in settings.jsx!)
import ProvidersTab from '../settings/tabs/ProvidersTab';
import PromptTab from '../settings/tabs/PromptTab';
import ConfigurationTab from '../settings/tabs/ConfigurationTab';
import ContextTab from '../settings/tabs/ContextTab';
import QualityTab from '../settings/tabs/QualityTab';
import PlaygroundTab from '../settings/tabs/PlaygroundTab';

// Import shared components
import TabsContainer from '../settings/shared/TabsContainer';

// Import constants
import { SETTINGS_TABS } from '../settings/constants/settingsTabs';

// Import utils
import { isWeightsSumValid } from '../settings/utils/settingsValidation';

const SettingsModal = ({ isOpen, onClose, preselectedImageId = null }) => {
	// State management
	const [settings, setSettings] = useState({});
	const [providers, setProviders] = useState({});
	const [qualityWeights, setQualityWeights] = useState({
		alt: 0.40,
		title: 0.30,
		caption: 0.20,
		keywords: 0.10
	});
	const [saving, setSaving] = useState(false);
	const [loading, setLoading] = useState(true);
	const [saveMessage, setSaveMessage] = useState(null);
	const [activeTab, setActiveTab] = useState('providers');
	const [showModal, setShowModal] = useState(false);
	const [modalMessage, setModalMessage] = useState('');

	// Validation
	const siteContextLength = (settings.site_context || '').length;
	const isSiteContextOverLimit = siteContextLength > 1500;

	// Load settings when modal opens
	useEffect(() => {
		if (isOpen) {
			loadSettings();
		}
	}, [isOpen]);

	const loadSettings = async () => {
		try {
			setLoading(true);
			const response = await apiFetch({
				path: '/ai-media/v1/settings',
				method: 'GET',
			});
			setSettings(response.settings || {});
			setProviders(response.providers || {});
			setQualityWeights(response.quality_weights || {
				alt: 0.40,
				title: 0.30,
				caption: 0.20,
				keywords: 0.10
			});
		} catch (err) {
			setSaveMessage({
				type: 'error',
				message: 'Error loading settings: ' + err.message
			});
		} finally {
			setLoading(false);
		}
	};

	const saveSettings = async () => {
		try {
			setSaving(true);
			setSaveMessage(null);

			const response = await apiFetch({
				path: '/ai-media/v1/settings',
				method: 'POST',
				data: {
					settings: settings,
					providers: providers,
					quality_weights: qualityWeights,
				},
			});

			setSaveMessage({
				type: 'success',
				message: response.message || 'Settings saved successfully!'
			});
		} catch (err) {
			setSaveMessage({
				type: 'error',
				message: 'Error saving settings: ' + err.message
			});
		} finally {
			setSaving(false);
		}
	};

	if (!isOpen) return null;

	return (
		<Modal
			title=""
			onRequestClose={onClose}
			className="ai-media-settings-modal"
			style={{
				maxWidth: '95vw',
				maxHeight: '95vh',
				width: '95vw',
				height: '95vh',
			}}
			__experimentalHideHeader={true}
		>
			<style>
				{`
					.ai-media-settings-modal .components-modal__content {
						padding: 0;
						margin: 0;
						display: flex;
						flex-direction: column;
						height: 100%;
					}
					.ai-media-settings-modal .components-modal__header {
						display: none !important;
					}
					.ai-media-settings .components-form-toggle.is-checked .components-form-toggle__thumb {
						left: 2px !important;
					}
				`}
			</style>

			<div className="ai-media-settings" style={{
				flex: 1,
				display: 'flex',
				flexDirection: 'column',
				overflow: 'hidden'
			}}>
				{/* Custom Header with Title and Close Button */}
				<div style={{
					padding: '20px 24px 16px',
					borderBottom: '1px solid #dcdcde',
					flexShrink: 0,
					display: 'flex',
					alignItems: 'center',
					justifyContent: 'space-between'
				}}>
					<h1 style={{ margin: 0, fontSize: '23px', fontWeight: 400, lineHeight: 1.3 }}>
						Plugin Settings
					</h1>
					<button
						type="button"
						onClick={onClose}
						style={{
							background: 'transparent',
							border: 'none',
							padding: '4px',
							cursor: 'pointer',
							display: 'flex',
							alignItems: 'center',
							justifyContent: 'center',
							borderRadius: '2px',
							color: '#646970'
						}}
						onMouseEnter={(e) => {
							e.currentTarget.style.background = '#f0f0f1';
						}}
						onMouseLeave={(e) => {
							e.currentTarget.style.background = 'transparent';
						}}
						aria-label="Close"
					>
						<span className="dashicons dashicons-no-alt"
							style={{ fontSize: '20px', width: '20px', height: '20px' }}>
						</span>
					</button>
				</div>

				{/* Tabs Container */}
				<TabsContainer
					tabs={SETTINGS_TABS}
					activeTab={activeTab}
					onTabChange={setActiveTab}
					onSave={saveSettings}
					saving={saving}
					disabled={loading || isSiteContextOverLimit || (activeTab === 'quality' && !isWeightsSumValid(qualityWeights))}
					disabledTitle={
						isSiteContextOverLimit
							? 'Site Context exceeds 1500 character limit'
							: (!isWeightsSumValid(qualityWeights) && activeTab === 'quality'
								? 'Quality weights must total 100% before saving'
								: '')
					}
				/>

				{saveMessage && (
					<div style={{ padding: '0 20px', paddingTop: '16px' }}>
						<AdminNotice
							notice={saveMessage}
							onDismiss={() => setSaveMessage(null)}
						/>
					</div>
				)}

				{showModal && (
					<Modal
						title="API Key Required"
						onRequestClose={() => setShowModal(false)}
					>
						<p>{modalMessage}</p>
						<Button variant="primary" onClick={() => setShowModal(false)}>
							OK
						</Button>
					</Modal>
				)}

				{loading ? (
					<div style={{ padding: '40px', textAlign: 'center' }}>
						<p>Loading settings...</p>
					</div>
				) : (
					<div className="ai-media-settings-content"
						style={{ flex: 1, overflow: 'auto', padding: '20px' }}>
						{activeTab === 'providers' && (
							<ProvidersTab
								providers={providers}
								setProviders={setProviders}
								setShowModal={setShowModal}
								setModalMessage={setModalMessage}
							/>
						)}

						{activeTab === 'prompt' && (
							<PromptTab
								settings={settings}
								setSettings={setSettings}
							/>
						)}

						{activeTab === 'context' && (
							<ContextTab
								settings={settings}
								setSettings={setSettings}
							/>
						)}

						{activeTab === 'configuration' && (
							<ConfigurationTab
								settings={settings}
								setSettings={setSettings}
							/>
						)}

						{activeTab === 'quality' && (
							<QualityTab
								settings={settings}
								setSettings={setSettings}
								qualityWeights={qualityWeights}
								setQualityWeights={setQualityWeights}
							/>
						)}

						{activeTab === 'playground' && (
							<PlaygroundTab preselectedImageId={preselectedImageId} />
						)}
					</div>
				)}
			</div>
		</Modal>
	);
};

export default SettingsModal;
