/**
 * Media Library Toolbar Component
 * Filters and search controls
 */

import { Button, TextControl } from '@wordpress/components';
import InteractiveSelectControl from '../shared/InteractiveSelectControl';

/**
 * MediaLibraryToolbar component
 * @param {Object} props - Component props
 * @param {string} props.searchInput - Search input value
 * @param {Function} props.onSearchChange - Callback when search input changes
 * @param {Function} props.onSearch - Callback when search is submitted
 * @param {Function} props.onSearchKeyDown - Callback for search key down events
 * @param {Function} props.onClearSearch - Callback when search is cleared
 * @param {string} props.statusFilter - Current status filter value
 * @param {Function} props.onStatusFilterChange - Callback when status filter changes
 * @param {Array} props.statusFilterOptions - Status filter options
 * @param {string} props.attachmentFilter - Current attachment filter value
 * @param {Function} props.onAttachmentFilterChange - Callback when attachment filter changes
 * @param {Array} props.attachmentFilterOptions - Attachment filter options
 * @returns {JSX.Element} Rendered toolbar
 */
const MediaLibraryToolbar = ({
	searchInput,
	onSearchChange,
	onSearch,
	onSearchKeyDown,
	onClearSearch,
	statusFilter,
	onStatusFilterChange,
	statusFilterOptions,
	attachmentFilter,
	onAttachmentFilterChange,
	attachmentFilterOptions,
}) => {
	return (
		<div className="ai-media-library-toolbar">
			<div className="ai-media-library-filters" style={{ alignItems: 'baseline' }}>
				<div className="search-control-wrapper">
					<div style={{ position: 'relative', flex: 1 }}>
						<TextControl
							label="Search"
							value={searchInput}
							onChange={onSearchChange}
							onKeyDown={onSearchKeyDown}
							placeholder="Search filename, ALT text, caption..."
						/>
						{searchInput && (
							<button
								type="button"
								className="search-clear-button"
								onClick={onClearSearch}
								aria-label="Clear search"
								title="Clear search"
							>
								
							</button>
						)}
					</div>
					<Button
						variant="primary"
						onClick={onSearch}
						className="search-button"
						style={{ marginBottom: '9px' }}
					>
						Search
					</Button>
				</div>

				<div style={{ flex: '0 0 auto', minWidth: '200px' }}>
					<InteractiveSelectControl
						label="Status"
						value={statusFilter}
						options={statusFilterOptions}
						onChange={onStatusFilterChange}
					/>
				</div>

				<div style={{ flex: '0 0 auto', minWidth: '200px' }}>
					<InteractiveSelectControl
						label="Attachment"
						value={attachmentFilter}
						options={attachmentFilterOptions}
						onChange={onAttachmentFilterChange}
					/>
				</div>
			</div>
		</div>
	);
};

export default MediaLibraryToolbar;
