/**
 * Attach to Post Modal
 * Allows searching for and attaching media to existing posts/pages
 */

import { Modal, Button } from '@wordpress/components';
import { formatDate } from '../../utils/formatters';

/**
 * AttachModal component
 * @param {Object} props - Component props
 * @param {boolean} props.isOpen - Whether modal is open
 * @param {Function} props.onClose - Callback to close modal
 * @param {string} props.search - Current search query
 * @param {Function} props.onSearchChange - Callback when search query changes
 * @param {Function} props.onSearch - Callback to perform search
 * @param {Function} props.onClearSearch - Callback to clear search
 * @param {boolean} props.searching - Whether search is in progress
 * @param {Array} props.results - Array of search results
 * @param {Object|null} props.selected - Currently selected post object
 * @param {Function} props.onSelect - Callback when post is selected
 * @param {Function} props.onConfirm - Callback when Select button is clicked
 * @returns {JSX.Element|null} Rendered modal or null if not open
 */
const AttachModal = ({
	isOpen,
	onClose,
	search,
	onSearchChange,
	onSearch,
	onClearSearch,
	searching,
	results = [],
	selected,
	onSelect,
	onConfirm,
}) => {
	if (!isOpen) {
		return null;
	}

	return (
		<Modal
			title="Attach to existing content"
			onRequestClose={onClose}
			className="ai-media-attach-modal"
			style={{ maxWidth: '720px', width: '90%' }}
		>
			<div className="find-box-search" style={{
				padding: '20px',
				background: '#fff',
				borderBottom: '1px solid #dcdcde'
			}}>
				<div style={{
					display: 'flex',
					gap: '12px',
					alignItems: 'flex-start'
				}}>
					<div style={{
						position: 'relative',
						flex: '1',
						minWidth: '0'
					}}>
						<input
							type="text"
							value={search}
							onChange={(e) => onSearchChange(e.target.value)}
							onKeyDown={(e) => e.key === 'Enter' && onSearch()}
							placeholder="Search posts, pages, and custom post types..."
							style={{
								width: '100%',
								padding: '8px 12px',
								paddingRight: search ? '32px' : '12px',
								fontSize: '14px',
								border: '1px solid #8c8f94',
								borderRadius: '3px',
								boxSizing: 'border-box',
								minHeight: '38px',
								transition: 'border-color 0.2s ease',
							}}
							onFocus={(e) => e.target.style.borderColor = '#2271b1'}
							onBlur={(e) => e.target.style.borderColor = '#8c8f94'}
						/>
						{search && (
							<button
								type="button"
								onClick={onClearSearch}
								style={{
									position: 'absolute',
									right: '8px',
									top: '50%',
									transform: 'translateY(-50%)',
									background: 'none',
									border: 'none',
									cursor: 'pointer',
									padding: '4px',
									color: '#646970',
									fontSize: '18px',
									lineHeight: '1',
									display: 'flex',
									alignItems: 'center',
									justifyContent: 'center',
									borderRadius: '3px',
								}}
								onMouseEnter={(e) => e.target.style.background = '#f0f0f1'}
								onMouseLeave={(e) => e.target.style.background = 'none'}
								title="Clear search"
							>
								
							</button>
						)}
					</div>
					<Button
						variant="primary"
						onClick={onSearch}
						isBusy={searching}
						disabled={searching}
						style={{
							minHeight: '38px',
							whiteSpace: 'nowrap'
						}}
					>
						Search
					</Button>
				</div>
			</div>

			<div className="find-box-response" style={{
				padding: '0'
			}}>
				{results.length === 0 ? (
					<div style={{
						display: 'flex',
						flexDirection: 'column',
						alignItems: 'center',
						justifyContent: 'center',
						padding: '60px 20px',
						color: '#646970',
						minHeight: '400px'
					}}>
						{searching ? (
							<>
								<div style={{
									width: '20px',
									height: '20px',
									border: '3px solid #f0f0f1',
									borderTop: '3px solid #2271b1',
									borderRadius: '50%',
									animation: 'spin 0.8s linear infinite',
									marginBottom: '16px'
								}} />
								<p style={{ margin: '0', fontSize: '14px', fontWeight: '500' }}>
									Searching content...
								</p>
							</>
						) : (
							<>
								<span style={{ fontSize: '48px', marginBottom: '16px', opacity: '0.3' }}></span>
								<p style={{ margin: '0', fontSize: '14px', fontWeight: '500', color: '#1d2327' }}>
									No content found
								</p>
								<p style={{ margin: '8px 0 0', fontSize: '13px' }}>
									Try searching for posts, pages, or other content types
								</p>
							</>
						)}
					</div>
				) : (
					<table className="widefat" style={{ marginBottom: '100px' }}>
						<thead>
							<tr>
								<th style={{ width: '40px' }}></th>
								<th>Title</th>
								<th style={{ whiteSpace: 'nowrap' }}>Type</th>
								<th style={{ whiteSpace: 'nowrap' }}>Date</th>
								<th style={{ whiteSpace: 'nowrap' }}>Status</th>
							</tr>
						</thead>
						<tbody>
							{results.map((result, index) => (
								<tr key={result.id} className={index % 2 === 0 ? '' : 'alternate'}>
									<td style={{ textAlign: 'center' }}>
										<input
											type="radio"
											name="found_post_id"
											value={result.id}
											checked={selected?.id === result.id}
											onChange={() => onSelect(result)}
											id={`found-${result.id}`}
										/>
									</td>
									<td>
										<label htmlFor={`found-${result.id}`} style={{ cursor: 'pointer' }}>
											{result.title}
										</label>
									</td>
									<td style={{ whiteSpace: 'nowrap' }}>{result.typeName || result.type}</td>
									<td style={{ whiteSpace: 'nowrap' }}>{formatDate(result.date)}</td>
									<td style={{ whiteSpace: 'nowrap' }}>{result.status}</td>
								</tr>
							))}
						</tbody>
					</table>
				)}
			</div>

			<div className="find-box-buttons" style={{
				padding: '20px',
				borderTop: '1px solid #dcdcde',
				display: 'flex',
				justifyContent: 'space-between',
				alignItems: 'center',
				gap: '16px',
				background: '#fff'
			}}>
				<div style={{ flex: '1', minWidth: '0' }}>
					{selected && (
						<div style={{
							fontSize: '13px',
							color: '#646970',
							display: 'flex',
							alignItems: 'center',
							gap: '8px'
						}}>
							<span style={{
								display: 'inline-block',
								width: '6px',
								height: '6px',
								borderRadius: '50%',
								background: '#00a32a'
							}} />
							<span style={{ fontWeight: '500', color: '#1d2327' }}>Selected:</span>
							<span style={{ overflow: 'hidden', textOverflow: 'ellipsis', whiteSpace: 'nowrap' }}>
								{selected.title}
							</span>
						</div>
					)}
				</div>
				<div style={{ display: 'flex', gap: '8px', flexShrink: '0' }}>
					<Button variant="secondary" onClick={onClose}>
						Close
					</Button>
					<Button
						variant="primary"
						onClick={onConfirm}
						disabled={!selected}
					>
						Select
					</Button>
				</div>
			</div>
		</Modal>
	);
};

export default AttachModal;
