/**
 * Media Library Entry Point
 * Initializes and renders the Media Library application
 */

import { render } from '@wordpress/element';
import MediaLibrary from './components/media-library/MediaLibrary';
import '../css/media-library.css';

// Initialize media library when DOM is ready
document.addEventListener('DOMContentLoaded', () => {
	const root = document.getElementById('ai-media-library-root');
	if (root) {
		render(<MediaLibrary />, root);
	}
});
