/**
 * Hook for managing file uploads
 */

import { useState, useRef } from '@wordpress/element';

/**
 * Manage upload state and provide upload utilities
 * @param {Function} onUploadComplete - Callback to run after successful upload (e.g., refresh attachments)
 * @returns {Object} Upload state and utilities
 */
export const useUpload = (onUploadComplete = null) => {
	const [uploadModalOpen, setUploadModalOpen] = useState(false);
	const [uploadFiles, setUploadFiles] = useState([]);
	const [uploading, setUploading] = useState(false);
	const [uploadProgress, setUploadProgress] = useState({}); // {filename: {status, progress, id}}
	const [isDragging, setIsDragging] = useState(false);
	const fileInputRef = useRef(null);

	/**
	 * Open upload modal
	 */
	const openUploadModal = () => {
		setUploadModalOpen(true);
	};

	/**
	 * Close upload modal
	 */
	const closeUploadModal = () => {
		if (uploading) {
			if (!confirm('Upload is in progress. Are you sure you want to cancel?')) {
				return;
			}
		}
		setUploadModalOpen(false);
		setUploadFiles([]);
		setUploadProgress({});
	};

	/**
	 * Handle file selection from input
	 * @param {Event} e - File input change event
	 */
	const handleFileSelect = (e) => {
		const files = Array.from(e.target.files);
		handleFilesAdded(files);
	};

	/**
	 * Handle files added (from input or drag & drop)
	 * @param {Array} files - Array of File objects
	 */
	const handleFilesAdded = (files) => {
		// Filter only image files
		const imageFiles = files.filter(file => file.type.startsWith('image/'));

		if (imageFiles.length === 0) {
			alert('Please select image files only.');
			return;
		}

		setUploadFiles(prev => [...prev, ...imageFiles]);

		// Initialize progress for new files
		const newProgress = {};
		imageFiles.forEach(file => {
			newProgress[file.name] = { status: 'pending', progress: 0, id: null };
		});
		setUploadProgress(prev => ({ ...prev, ...newProgress }));
	};

	/**
	 * Start uploading selected files
	 */
	const handleStartUpload = async () => {
		if (uploadFiles.length === 0 || uploading) return;

		setUploading(true);

		for (const file of uploadFiles) {
			try {
				// Update status to uploading
				setUploadProgress(prev => ({
					...prev,
					[file.name]: { ...prev[file.name], status: 'uploading', progress: 0 }
				}));

				// Create form data
				const formData = new FormData();
				formData.append('file', file);
				formData.append('title', file.name.replace(/\.[^/.]+$/, '')); // Remove extension


				// Upload file using WordPress REST API with proper headers
				const response = await fetch(window.aiMediaSEO.wpApiUrl + 'wp/v2/media', {
					method: 'POST',
					headers: {
						'X-WP-Nonce': window.aiMediaSEO.nonce,
					},
					body: formData,
				});


				if (!response.ok) {
					const errorData = await response.json();
					throw new Error(errorData.message || `Upload failed: ${response.statusText}`);
				}

				const result = await response.json();

				// Update progress to completed
				setUploadProgress(prev => ({
					...prev,
					[file.name]: { status: 'completed', progress: 100, id: result.id }
				}));

			} catch (error) {
				setUploadProgress(prev => ({
					...prev,
					[file.name]: { status: 'failed', progress: 0, id: null, error: error.message }
				}));
			}
		}

		setUploading(false);

		// Check if all uploads completed successfully (need to check final state)
		setTimeout(() => {
			setUploadProgress(currentProgress => {
				const allCompleted = Object.values(currentProgress).every(p => p.status === 'completed');

				if (allCompleted && Object.keys(currentProgress).length > 0) {
					// All uploads completed successfully
					setTimeout(() => {
						setUploadModalOpen(false);
						setUploadFiles([]);
						setUploadProgress({});
						if (onUploadComplete) {
							onUploadComplete(); // Refresh the library
						}
					}, 800);
				}

				return currentProgress;
			});
		}, 100);
	};

	/**
	 * Remove a file from upload list
	 * @param {string} fileName - Name of file to remove
	 */
	const handleRemoveFile = (fileName) => {
		setUploadFiles(prev => prev.filter(f => f.name !== fileName));
		setUploadProgress(prev => {
			const newProgress = { ...prev };
			delete newProgress[fileName];
			return newProgress;
		});
	};

	// Drag & drop handlers
	const handleDragEnter = (e) => {
		e.preventDefault();
		e.stopPropagation();
		setIsDragging(true);
	};

	const handleDragLeave = (e) => {
		e.preventDefault();
		e.stopPropagation();
		// Only set dragging to false if leaving the drop zone entirely
		if (e.currentTarget === e.target) {
			setIsDragging(false);
		}
	};

	const handleDragOver = (e) => {
		e.preventDefault();
		e.stopPropagation();
	};

	const handleDrop = (e) => {
		e.preventDefault();
		e.stopPropagation();
		setIsDragging(false);

		const files = Array.from(e.dataTransfer.files);
		handleFilesAdded(files);
	};

	return {
		uploadModalOpen,
		uploadFiles,
		uploading,
		uploadProgress,
		isDragging,
		fileInputRef,
		openUploadModal,
		closeUploadModal,
		handleFileSelect,
		handleFilesAdded,
		handleStartUpload,
		handleRemoveFile,
		handleDragEnter,
		handleDragLeave,
		handleDragOver,
		handleDrop,
	};
};
