/**
 * Caption Cell
 * Editable caption with draft support
 */

import { stripHtml } from '../../utils/formatters';
import { isAttachmentProcessing } from '../../utils/attachmentHelpers';

/**
 * CaptionCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object
 * @param {Object} props.editingCaption - Editing state {id, value}
 * @param {boolean} props.savingCaption - Whether save is in progress
 * @param {Object} props.queueStatus - Queue status object
 * @param {Function} props.onCaptionClick - Callback when caption is clicked
 * @param {Function} props.onCaptionChange - Callback when caption changes
 * @param {Function} props.onCaptionKeyDown - Callback for key down events
 * @param {Function} props.onCaptionSave - Callback to save caption
 * @returns {JSX.Element} Rendered caption cell
 */
const CaptionCell = ({
	attachment,
	editingCaption,
	savingCaption,
	queueStatus,
	onCaptionClick,
	onCaptionChange,
	onCaptionKeyDown,
	onCaptionSave,
}) => {
	const isDraft = attachment.meta?._ai_media_status === 'processed';
	const draftCaption = attachment.meta?._ai_media_draft_caption || '';
	const actualCaption = stripHtml(attachment.caption?.rendered) || '';
	const displayCaption = isDraft && draftCaption ? draftCaption : actualCaption;
	const isProcessing = isAttachmentProcessing(attachment, queueStatus);

	// Use smaller width for empty cells to prevent table overflow
	const hasContent = displayCaption && !isProcessing;

	return (
		<td style={{
			cursor: editingCaption && editingCaption.id === attachment.id ? 'default' : 'pointer',
			minWidth: hasContent ? '150px' : '100px',
			maxWidth: '280px',
			width: hasContent ? 'auto' : '120px'
		}}>
			{editingCaption && editingCaption.id === attachment.id ? (
				<textarea
					value={editingCaption.value}
					onChange={(e) => onCaptionChange(e.target.value)}
					onKeyDown={onCaptionKeyDown}
					onBlur={onCaptionSave}
					disabled={savingCaption}
					autoFocus
					rows={3}
					onClick={(e) => e.stopPropagation()}
					style={{
						width: '100%',
						padding: '4px 8px',
						border: '1px solid #8c8f94',
						borderRadius: '2px',
						fontSize: '13px',
						resize: 'vertical',
						fontFamily: 'inherit'
					}}
				/>
			) : (
				<span
					onClick={() => onCaptionClick(attachment)}
					style={{
						display: 'block',
						padding: '4px 0',
						cursor: isProcessing ? 'not-allowed' : 'pointer',
						opacity: isProcessing ? 0.6 : 1,
						color: '#666',
						fontSize: '13px'
					}}
				>
					{isProcessing ? (
						<em style={{ color: '#0071a1' }}>Processing...</em>
					) : !displayCaption ? (
						<em style={{ color: '#999' }}>Click to add caption</em>
					) : isDraft && draftCaption ? (
						<span style={{
							backgroundColor: '#fff3cd',
							padding: '2px 4px',
							borderRadius: '2px',
							display: 'inline-block'
						}}>
							<strong style={{ color: '#856404', fontSize: '11px', marginRight: '4px' }}>DRAFT:</strong>
							{displayCaption}
						</span>
					) : (
						displayCaption
					)}
				</span>
			)}
		</td>
	);
};

export default CaptionCell;
