/**
 * Provider Cell
 * Displays AI model/provider information
 */

/**
 * ProviderCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object
 * @returns {JSX.Element} Rendered provider cell
 */
const ProviderCell = ({ attachment }) => {
	return (
		<td>
			{attachment.model ? (
				<div style={{
					fontSize: '11px',
					color: '#666'
				}}>
					{attachment.model}
				</div>
			) : (
				<span style={{ color: '#999' }}></span>
			)}
		</td>
	);
};

export default ProviderCell;
