/**
 * Speed Mode Setup Modal
 * Shows before batch processing starts to allow user to select speed mode
 * @version 2.2.0
 */

import { useState } from '@wordpress/element';
import SpeedModeSelector from '../shared/SpeedModeSelector';
import './SpeedModeSetupModal.css';

/**
 * SpeedModeSetupModal component
 * @param {Object} props - Component props
 * @param {boolean} props.isOpen - Whether modal is open
 * @param {number} props.imageCount - Number of images to process
 * @param {Function} props.onConfirm - Callback when user confirms (speedMode, concurrency)
 * @param {Function} props.onCancel - Callback when user cancels
 * @returns {JSX.Element|null} Rendered modal or null if not open
 */
const SpeedModeSetupModal = ({ isOpen, imageCount, onConfirm, onCancel }) => {
	const [selectedMode, setSelectedMode] = useState('optimal');
	const [concurrency, setConcurrency] = useState(8);

	if (!isOpen) {
		return null;
	}

	const handleConfirm = () => {
		onConfirm(selectedMode, concurrency);
	};

	// Callback from SpeedModeSelector to update concurrency when mode changes
	const handleModeChange = (mode) => {
		setSelectedMode(mode);
		// SpeedModeSelector will handle concurrency internally
	};

	return (
		<div className="speed-mode-setup-overlay">
			<div className="speed-mode-setup-modal">
				<h2>Batch Processing Setup</h2>
				<p className="modal-description">
					Select processing speed for batch processing {imageCount} images.
					The plugin has automatically detected your API tier and recommends optimal settings.
				</p>

				<SpeedModeSelector
					selectedMode={selectedMode}
					onModeChange={handleModeChange}
					imageCount={imageCount}
				/>

				<div className="modal-actions">
					<button
						type="button"
						className="button button-secondary"
						onClick={onCancel}
					>
						Cancel
					</button>
					<button
						type="button"
						className="button button-primary"
						onClick={handleConfirm}
						style={{ marginLeft: '8px' }}
					>
						Start Processing
					</button>
				</div>
			</div>
		</div>
	);
};

export default SpeedModeSetupModal;
