const MessageRotator = ({ currentProgress, totalImages }) => {
	const [currentMessage, setCurrentMessage] = useState(TECH_MESSAGES[0]);
	const [fadeOut, setFadeOut] = useState(false);

	// Check for milestone
	useEffect(() => {
		if (MILESTONE_MESSAGES[currentProgress]) {
			setFadeOut(true);
			setTimeout(() => {
				setCurrentMessage(MILESTONE_MESSAGES[currentProgress]);
				setFadeOut(false);
			}, 300);

			// Return to random messages after 5 seconds
			setTimeout(() => {
				const randomMsg = TECH_MESSAGES[
					Math.floor(Math.random() * TECH_MESSAGES.length)
				];
				setFadeOut(true);
				setTimeout(() => {
					setCurrentMessage(randomMsg);
					setFadeOut(false);
				}, 300);
			}, 5000);
		}
	}, [currentProgress]);

	// Rotate messages every 5 seconds
	useEffect(() => {
		const interval = setInterval(() => {
			setFadeOut(true);
			setTimeout(() => {
				const randomMsg = TECH_MESSAGES[
					Math.floor(Math.random() * TECH_MESSAGES.length)
				];
				setCurrentMessage(randomMsg);
				setFadeOut(false);
			}, 300);
		}, 5000);

		return () => clearInterval(interval);
	}, []);

	return (
		<div className="message-rotator">
			<div className={`message-content ${fadeOut ? 'fade-out' : 'fade-in'}`}>
				<span className="message-icon">{currentMessage.icon}</span>
				<span className="message-text">{currentMessage.text}</span>
			</div>
		</div>
	);
};

export default MessageRotator;
