/**
 * Hook for managing filter state in the media library
 *
 * Simple state management for filters. Server-side filtering is handled by the API endpoint.
 */

import { useState } from '@wordpress/element';
import { DEFAULTS } from '../utils/constants';

/**
 * Manage filter state
 * @returns {Object} Filter state and setters
 */
export const useFilters = () => {
	const [statusFilter, setStatusFilter] = useState(DEFAULTS.STATUS_FILTER);
	const [attachmentFilter, setAttachmentFilter] = useState(DEFAULTS.ATTACHMENT_FILTER);
	const [searchQuery, setSearchQuery] = useState(DEFAULTS.SEARCH_QUERY);
	const [searchInput, setSearchInput] = useState('');

	/**
	 * Handle search button click
	 */
	const handleSearch = () => {
		setSearchQuery(searchInput);
	};

	/**
	 * Handle search input key down
	 * @param {Event} e - Keyboard event
	 */
	const handleSearchKeyDown = (e) => {
		if (e.key === 'Enter') {
			handleSearch();
		}
	};

	return {
		statusFilter,
		setStatusFilter,
		attachmentFilter,
		setAttachmentFilter,
		searchQuery,
		setSearchQuery,
		searchInput,
		setSearchInput,
		handleSearch,
		handleSearchKeyDown,
	};
};
