/**
 * Speed Mode Selector Component
 * Allows users to select batch processing speed mode based on their API tier
 */

import { useState, useEffect } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import './SpeedModeSelector.css';

/**
 * Speed Modes Configuration
 */
const SPEED_MODES = {
	easy: {
		label: 'Easy (Safe)',
		description: 'Maximum stability, minimal rate limit risk',
		icon: '',
		riskLevel: 'low',
	},
	optimal: {
		label: 'Optimal (Balanced)',
		description: 'Optimal balance between speed and safety',
		icon: '',
		riskLevel: 'medium',
		recommended: true,
	},
	extreme: {
		label: 'Extreme (Maximum)',
		description: 'Maximum speed, high rate limit risk',
		icon: '',
		riskLevel: 'high',
		warning: 'Only for dedicated API keys and enterprise tiers',
	},
};

/**
 * Get recommendation text based on tier RPM
 * @param {number} rpm - Rate limit in requests per minute
 * @returns {string} Recommendation text
 */
const getRecommendationForTier = (rpm) => {
	if (rpm >= 1000) {
		return 'Your tier supports Extreme mode for maximum performance. Feel free to push the limits!';
	} else if (rpm >= 300) {
		return 'With your tier, you can safely use Extreme mode for fast processing.';
	} else if (rpm >= 50) {
		return 'Optimal mode is perfect for your tier';
	} else {
		return 'We recommend Easy mode for your tier to avoid hitting rate limits.';
	}
};

/**
 * SpeedModeSelector component
 * @param {Object} props - Component props
 * @param {string} props.selectedMode - Currently selected speed mode
 * @param {Function} props.onModeChange - Callback when mode changes
 * @param {number} props.imageCount - Number of images to process
 * @returns {JSX.Element} Speed mode selector
 */
const SpeedModeSelector = ({ selectedMode = 'optimal', onModeChange, imageCount = 0 }) => {
	//  STEDN: Start with CONSERVATIVE default values (Tier 1, 15 RPM)
	// Modal shows immediately with safe defaults, then updates when tier is detected
	const [detectedTier, setDetectedTier] = useState({
		google: {
			tier: 'tier_1',
			tier_name: 'Detecting...',
			rpm: 15,
			confidence: 0.5,
			description: 'Detecting your API tier...',
		},
	});
	const [recommendedConcurrency, setRecommendedConcurrency] = useState({
		easy: 1,
		optimal: 2,
		extreme: 3,
	});
	const [loading, setLoading] = useState(false); // Start false - we have defaults

	// Load tier detection on mount (runs in background)
	useEffect(() => {
		detectApiTiers();
	}, []);

	/**
	 * Detect API tiers from backend
	 */
	const detectApiTiers = async () => {
		try {
			//  QUICK WIN 2: Use cache (24h) instead of force refresh
			// First load from cache is instant, background detection updates if needed
			const response = await apiFetch({
				path: '/ai-media/v1/settings/detect-tiers',
				method: 'POST',
				data: {
					force_refresh: false, // Use cache for speed (MediaLibrary pre-fetched it)
				},
			});

			if (response.success && response.tiers) {
				setDetectedTier(response.tiers);

				// Get primary provider tier
				const primaryProvider = Object.keys(response.tiers)[0];
				if (primaryProvider && response.tiers[primaryProvider].recommended_concurrency) {
					setRecommendedConcurrency(
						response.tiers[primaryProvider].recommended_concurrency
					);
				}
			}
		} catch (error) {
			console.error('Failed to detect API tiers:', error);
			// Fallback to defaults
			setRecommendedConcurrency({
				easy: 3,
				optimal: 8,
				extreme: 12,
			});
		} finally {
			setLoading(false);
		}
	};

	/**
	 * Calculate estimated processing time based on real rate limits
	 * @param {number} count - Image count
	 * @param {number} concurrency - Concurrent requests
	 * @param {number} rpm - Rate limit (requests per minute)
	 * @returns {string} Formatted time estimate
	 */
	const calculateEstimatedTime = (count, concurrency, rpm = null) => {
		if (!count) return 'N/A';

		// If no RPM detected, use fallback calculation
		if (!rpm) {
			const avgTimePerRequest = 3.5;
			const totalTime = (count / concurrency) * avgTimePerRequest;

			if (totalTime < 60) {
				return `~${Math.round(totalTime)}s`;
			}
			return `~${Math.round(totalTime / 60)}min`;
		}

		// Real calculation with rate limit bottleneck
		// Average API response time: 3-4 seconds
		const avgResponseTime = 3.5;

		// Theoretical throughput (requests/min) without rate limits
		const theoreticalPerMin = (60 / avgResponseTime) * concurrency;

		// Actual throughput is limited by the smaller of:
		// 1. Theoretical concurrency throughput
		// 2. API rate limit (RPM)
		const actualPerMin = Math.min(theoreticalPerMin, rpm);

		// Calculate total time in minutes
		// Add 15% buffer for retries, network delays, etc.
		const totalMinutes = (count / actualPerMin) * 1.15;
		const totalSeconds = totalMinutes * 60;

		if (totalSeconds < 60) {
			return `~${Math.round(totalSeconds)}s`;
		} else if (totalSeconds < 3600) {
			return `~${Math.round(totalMinutes)}min`;
		} else {
			const hours = Math.floor(totalMinutes / 60);
			const mins = Math.round(totalMinutes % 60);
			return `~${hours}h ${mins}min`;
		}
	};

	/**
	 * Get primary provider info
	 */
	const getPrimaryProviderInfo = () => {
		if (!detectedTier) return null;

		const primaryProvider = Object.keys(detectedTier)[0];
		if (!primaryProvider) return null;

		return {
			name: primaryProvider,
			...detectedTier[primaryProvider],
		};
	};

	const primaryProvider = getPrimaryProviderInfo();

	if (loading) {
		return (
			<div className="speed-mode-selector loading">
				<p>Detecting API tier...</p>
			</div>
		);
	}

	return (
		<div className="speed-mode-selector">
			<h3>Processing Speed</h3>

			{primaryProvider && (
				<div className="tier-info">
					<div style={{
						display: 'grid',
						gridTemplateColumns: '1fr 1fr',
						gap: '16px',
						alignItems: 'start'
					}}>
						<div>
							<p>
								<strong>Detected API Tier:</strong> {primaryProvider.tier_name}
								{primaryProvider.description && (
									<>
										{' - '}
										<span style={{
											fontStyle: 'italic',
											fontSize: '12px',
											color: '#787c82'
										}}>
											{primaryProvider.description}
										</span>
									</>
								)}
							</p>
							<p className="tier-details">
								Rate Limit: {primaryProvider.rpm} Requests/min  Confidence: {(primaryProvider.confidence * 100).toFixed(0)}%
							</p>
						</div>
						<div className="tier-recommendation" style={{
							borderRadius: '3px',
							fontSize: '12px',
							color: '#0066cc',
							fontWeight: '500',
							height: '100%',
							display: 'flex',
							alignItems: 'flex-start'
						}}>
							 {getRecommendationForTier(primaryProvider.rpm)}
						</div>
					</div>
				</div>
			)}

			<div className="speed-modes">
				{Object.entries(SPEED_MODES).map(([key, mode]) => {
					const concurrency = recommendedConcurrency[key] || 3;
					const estimatedTime = imageCount > 0
						? calculateEstimatedTime(imageCount, concurrency, primaryProvider?.rpm)
						: 'N/A';
					const isSelected = selectedMode === key;

					// Check if concurrency is too high for the rate limit
					const isInefficient = primaryProvider && concurrency > (primaryProvider.rpm / 3);

					return (
						<div
							key={key}
							className={`speed-mode ${isSelected ? 'active' : ''} ${mode.riskLevel}`}
							onClick={() => onModeChange(key)}
							role="button"
							tabIndex={0}
							onKeyPress={(e) => {
								if (e.key === 'Enter' || e.key === ' ') {
									onModeChange(key);
								}
							}}
						>
							<div className="mode-header">
								<span className="icon">{mode.icon}</span>
								<span className="label">{mode.label}</span>
								{mode.recommended && (
									<span className="badge">Recommended</span>
								)}
							</div>

							<p className="description">{mode.description}</p>

							<div className="mode-stats">
								<div className="stat">
									<span className="stat-label">Concurrency:</span>
									<span className="stat-value">{concurrency} simultaneous</span>
								</div>
								{imageCount > 0 && (
									<div className="stat">
										<span className="stat-label">Time ({imageCount} images):</span>
										<span className="stat-value">{estimatedTime}</span>
									</div>
								)}
								<div className="stat">
									<span className="stat-label">Risk:</span>
									<span className={`stat-value risk-${mode.riskLevel}`}>
										{mode.riskLevel === 'low' && 'Low'}
										{mode.riskLevel === 'medium' && 'Medium'}
										{mode.riskLevel === 'high' && 'High'}
									</span>
								</div>
							</div>

							{mode.warning && (
								<div className="warning">
									 {mode.warning}
								</div>
							)}

							{isInefficient && (
								<div className="warning" style={{
									background: '#fff3cd',
									color: '#856404',
									border: '1px solid #ffc107',
								}}>
									 Note: With your rate limit ({primaryProvider.rpm} RPM), concurrency above {Math.floor(primaryProvider.rpm / 3)} won't significantly improve speed.
								</div>
							)}
						</div>
					);
				})}
			</div>

			<div className="speed-mode-help">
				<p className="help-text">
					<strong>Tip:</strong> Optimal mode is recommended for most users.
					Use Extreme mode only if you have a dedicated API key and high tier.
				</p>
			</div>
		</div>
	);
};

export default SpeedModeSelector;
