/**
 * Alt Text Cell
 * Editable ALT text with draft support
 */

import { isAttachmentProcessing } from '../../utils/attachmentHelpers';

/**
 * AltTextCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object
 * @param {Object} props.editingAlt - Editing state {id, value}
 * @param {boolean} props.savingAlt - Whether save is in progress
 * @param {Object} props.queueStatus - Queue status object
 * @param {Function} props.onAltClick - Callback when ALT text is clicked
 * @param {Function} props.onAltChange - Callback when ALT text changes
 * @param {Function} props.onAltKeyDown - Callback for key down events
 * @param {Function} props.onAltSave - Callback to save ALT text
 * @returns {JSX.Element} Rendered ALT text cell
 */
const AltTextCell = ({
	attachment,
	editingAlt,
	savingAlt,
	queueStatus,
	onAltClick,
	onAltChange,
	onAltKeyDown,
	onAltSave,
}) => {
	const isDraft = attachment.meta?._ai_media_status === 'processed';
	const draftAlt = attachment.meta?._ai_media_draft_alt || '';
	const actualAlt = attachment.alt_text || '';
	const displayAlt = isDraft && draftAlt ? draftAlt : actualAlt;
	const isProcessing = isAttachmentProcessing(attachment, queueStatus);

	// Use smaller width for empty cells to prevent table overflow
	const hasContent = displayAlt && !isProcessing;

	return (
		<td style={{
			cursor: editingAlt && editingAlt.id === attachment.id ? 'default' : 'pointer',
			minWidth: hasContent ? '150px' : '100px',
			maxWidth: '300px',
			width: hasContent ? 'auto' : '120px'
		}}>
			{editingAlt && editingAlt.id === attachment.id ? (
				<textarea
					value={editingAlt.value}
					onChange={(e) => onAltChange(e.target.value)}
					onKeyDown={onAltKeyDown}
					onBlur={onAltSave}
					disabled={savingAlt}
					autoFocus
					rows={3}
					onClick={(e) => e.stopPropagation()}
					style={{
						width: '100%',
						padding: '4px 8px',
						border: '1px solid #8c8f94',
						borderRadius: '2px',
						fontSize: '13px',
						resize: 'vertical',
						fontFamily: 'inherit'
					}}
				/>
			) : (
				<span
					onClick={() => onAltClick(attachment)}
					style={{
						display: 'block',
						padding: '4px 0',
						cursor: isProcessing ? 'not-allowed' : 'pointer',
						opacity: isProcessing ? 0.6 : 1
					}}
				>
					{isProcessing ? (
						<em style={{ color: '#0071a1' }}>Processing...</em>
					) : !displayAlt ? (
						<em style={{ color: '#999' }}>Click to add ALT text</em>
					) : isDraft && draftAlt ? (
						<span style={{
							backgroundColor: '#fff3cd',
							padding: '2px 4px',
							borderRadius: '2px',
							display: 'inline-block'
						}}>
							<strong style={{ color: '#856404', fontSize: '11px', marginRight: '4px' }}>DRAFT:</strong>
							{displayAlt}
						</span>
					) : (
						displayAlt
					)}
				</span>
			)}
		</td>
	);
};

export default AltTextCell;
