/**
 * Simple Batch Processing Modal
 * Lightweight modal for batches  20 images
 * (Current design with minor enhancements)
 * @version 3.0.0
 */
import { useState } from '@wordpress/element';

const SimpleBatchModal = ({
	batchProgress,
	processingStatus,
	onCancel,
	onClose,
	cancelledRef
}) => {
	const [isCancelling, setIsCancelling] = useState(false);

	const handleButtonClick = () => {
		if (batchProgress.current === batchProgress.total) {
			onClose();
		} else {
			setIsCancelling(true);
			if (cancelledRef) {
				cancelledRef.current = true;
			}
			onCancel();
		}
	};

	return (
		<div className="batch-processing-overlay">
			<div className="batch-processing-modal">
				<h2>AI Processing in Progress</h2>
				<p className="batch-processing-description">
					Processing {batchProgress.total} images with AI.
					This may take a few minutes. <strong>Please do not close this window</strong>.
				</p>

				{isCancelling && (
					<div className="cancelling-notice" style={{
						margin: '15px 0',
						padding: '12px',
						background: '#fff3cd',
						border: '1px solid #ffc107',
						borderRadius: '3px',
						color: '#856404',
						fontWeight: '600',
						textAlign: 'center',
					}}>
						 Cancelling batch processing... finishing current requests.
					</div>
				)}

				{processingStatus && (
					<div className="processing-status-message">
						{processingStatus}
					</div>
				)}

				<div className="progress-bar-container">
					<div
						className="progress-bar-fill"
						style={{
							width: `${(batchProgress.current / batchProgress.total) * 100}%`
						}}
					/>
				</div>

				<div className="progress-stats">
					<div className="progress-percentage">
						{Math.round((batchProgress.current / batchProgress.total) * 100)}%
					</div>
					<div className="progress-text">
						{batchProgress.current} / {batchProgress.total} images
					</div>
				</div>

				<div className="batch-results">
					<div className="batch-result-item">
						<div className="result-label">Successful</div>
						<div className="success-count">{batchProgress.success.length}</div>
					</div>
					<div className="batch-result-item">
						<div className="result-label">Failed</div>
						<div className="failed-count">{batchProgress.failed.length}</div>
					</div>
					{batchProgress.retried > 0 && (
						<div className="batch-result-item">
							<div className="result-label">Retried</div>
							<div className="retried-count" style={{ color: '#f0b849' }}>
								{batchProgress.retried}
							</div>
						</div>
					)}
				</div>

				<div className="modal-actions">
					<button
						type="button"
						onClick={handleButtonClick}
						disabled={isCancelling}
					>
						{batchProgress.current === batchProgress.total ? 'Close' : 'Cancel Processing'}
					</button>
				</div>
			</div>
		</div>
	);
};

export default SimpleBatchModal;
