/**
 * Live Image Feed
 * Horizontal scrolling carousel of recently processed images
 * @version 3.0.0
 */
import { useEffect, useRef } from '@wordpress/element';
import './LiveImageFeed.css';

const LiveImageFeed = ({ recentlyProcessed }) => {
	const scrollRef = useRef(null);

	// Auto-scroll to newest image
	useEffect(() => {
		if (scrollRef.current && recentlyProcessed.length > 0) {
			scrollRef.current.scrollTo({
				left: 0,
				behavior: 'smooth'
			});
		}
	}, [recentlyProcessed.length]);

	if (recentlyProcessed.length === 0) {
		return (
			<div className="live-feed-empty">
				<div className="empty-icon"></div>
				<div className="empty-text">Waiting for first image...</div>
			</div>
		);
	}

	return (
		<div className="live-image-feed">
			<div className="feed-header">
				<span className="feed-icon"></span>
				<span className="feed-title">Live Feed</span>
				<span className="feed-count">{recentlyProcessed.length} recent</span>
			</div>

			<div className="feed-scroll-container" ref={scrollRef}>
				{recentlyProcessed.map((image, index) => (
					<div
						key={`${image.id}-${image.timestamp}`}
						className="feed-item"
						style={{
							animationDelay: `${index * 0.05}s`
						}}
					>
						<div className="feed-item-image">
							<img
								src={image.thumbnail}
								alt={image.alt}
								loading="lazy"
							/>
							<div className="feed-item-overlay">
								<span className="success-check"></span>
							</div>
						</div>

						<div className="feed-item-badge">
							{image.provider === 'openai' && (
								<span className="provider-badge openai" title="OpenAI">
									 GPT
								</span>
							)}
							{image.provider === 'google' && (
								<span className="provider-badge google" title="Google">
									 Gemini
								</span>
							)}
						</div>

						<div className="feed-item-alt" title={image.alt}>
							{image.alt.length > 30
								? image.alt.substring(0, 30) + '...'
								: image.alt
							}
						</div>
					</div>
				))}
			</div>
		</div>
	);
};

export default LiveImageFeed;
