/**
 * Status Cell
 * Editable status with approve checkbox
 */

import { isAttachmentProcessing } from '../../utils/attachmentHelpers';

/**
 * StatusCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object
 * @param {Object} props.editingStatus - Editing state {id, value}
 * @param {boolean} props.savingStatus - Whether save is in progress
 * @param {Object} props.queueStatus - Queue status object
 * @param {Function} props.onStatusClick - Callback when status is clicked
 * @param {Function} props.onStatusChange - Callback when status changes
 * @param {Function} props.onStatusSave - Callback to save status
 * @param {Function} props.onApprove - Callback to approve processed item
 * @returns {JSX.Element} Rendered status cell
 */
const StatusCell = ({
	attachment,
	editingStatus,
	savingStatus,
	queueStatus,
	onStatusClick,
	onStatusChange,
	onStatusSave,
	onApprove,
}) => {
	const isProcessing = isAttachmentProcessing(attachment, queueStatus);

	return (
		<td>
			{editingStatus && editingStatus.id === attachment.id ? (
				<select
					value={editingStatus.value}
					onChange={(e) => onStatusChange(e.target.value)}
					onBlur={onStatusSave}
					disabled={savingStatus}
					autoFocus
					onClick={(e) => e.stopPropagation()}
					style={{
						padding: '4px 8px',
						border: '1px solid #8c8f94',
						borderRadius: '2px',
						fontSize: '13px'
					}}
				>
					<option value="pending">Pending</option>
					<option value="processed">Processed</option>
					<option value="approved">Approved</option>
				</select>
			) : (
				<div style={{ display: 'flex', alignItems: 'center', gap: '8px' }}>
					<span
						className={`ai-media-status ai-media-status-${
							queueStatus[attachment.id]?.in_queue ? 'processing' :
							(attachment.meta?._ai_media_status || 'pending')
						}`}
						onClick={() => onStatusClick(attachment)}
						style={{
							cursor: isProcessing ? 'not-allowed' : 'pointer',
							opacity: isProcessing ? 0.8 : 1
						}}
					>
						{(() => {
							const displayStatus = queueStatus[attachment.id]?.in_queue ? 'processing' :
												(attachment.meta?._ai_media_status || 'pending');
							return displayStatus.charAt(0).toUpperCase() + displayStatus.slice(1);
						})()}
					</span>
					{attachment.meta?._ai_media_status === 'processed' && (
						<input
							type="checkbox"
							onChange={() => onApprove(attachment)}
							disabled={savingStatus || isProcessing}
							title="Approve"
						/>
					)}
				</div>
			)}
		</td>
	);
};

export default StatusCell;
