/**
 * Enhanced Batch Processing Modal
 * Full-featured modal for batches > 20 images
 * @version 3.0.0
 */
import { useState } from '@wordpress/element';
import LiveImageFeed from './LiveImageFeed';
import MessageRotator from './MessageRotator';
import LiveStats from './LiveStats';
import './EnhancedBatchModal.css';

const EnhancedBatchModal = ({
	batchProgress,
	processingStatus,
	onCancel,
	onClose,
	cancelledRef
}) => {
	const [isCancelling, setIsCancelling] = useState(false);

	const handleButtonClick = () => {
		if (batchProgress.current === batchProgress.total) {
			onClose();
		} else {
			setIsCancelling(true);
			if (cancelledRef) {
				cancelledRef.current = true;
			}
			onCancel();
		}
	};

	return (
		<div className="batch-processing-overlay enhanced">
			<div className="batch-processing-modal enhanced">
				<div className="modal-header">
					<h2> AI Processing Cinema</h2>
					<div className="header-subtitle">
						Sit back, relax, and watch the magic happen 
					</div>
				</div>

				{/* Cancelling Notice */}
				{isCancelling && (
					<div className="cancelling-notice">
						 Cancelling batch processing... finishing current requests.
					</div>
				)}

				{/* Live Image Feed */}
				<LiveImageFeed recentlyProcessed={batchProgress.recentlyProcessed || []} />

				{/* AI Comedy Messages */}
				<MessageRotator
					currentProgress={batchProgress.current}
					totalImages={batchProgress.total}
				/>

				{/* Progress Bar */}
				<div className="enhanced-progress-section">
					<div className="progress-bar-container enhanced">
						<div
							className="progress-bar-fill enhanced"
							style={{
								width: `${(batchProgress.current / batchProgress.total) * 100}%`
							}}
						/>
					</div>

					<div className="progress-stats-row">
						<div className="progress-percentage">
							{Math.round((batchProgress.current / batchProgress.total) * 100)}%
						</div>
						<div className="progress-text">
							{batchProgress.current} / {batchProgress.total} images
						</div>
					</div>
				</div>

				{/* Live Statistics */}
				<LiveStats batchProgress={batchProgress} />

				{/* Batch Results Summary */}
				<div className="batch-results enhanced">
					<div className="batch-result-item success">
						<div className="result-value">
							{batchProgress.success.length} <span className="result-icon"></span>
						</div>
						<div className="result-label">Successful</div>
					</div>

					<div className="batch-result-item failed">
						<div className="result-value">
							{batchProgress.failed.length} <span className="result-icon"></span>
						</div>
						<div className="result-label">Failed</div>
					</div>

					{batchProgress.retried > 0 && (
						<div className="batch-result-item retried">
							<div className="result-value">
								{batchProgress.retried} <span className="result-icon"></span>
							</div>
							<div className="result-label">Retried</div>
						</div>
					)}
				</div>

				{/* Action Buttons */}
				<div className="modal-actions">
					<button
						type="button"
						className="action-button"
						onClick={handleButtonClick}
						disabled={isCancelling}
					>
						{batchProgress.current === batchProgress.total
							? ' Close'
							: ' Cancel Processing'
						}
					</button>
				</div>
			</div>
		</div>
	);
};

export default EnhancedBatchModal;
