/**
 * Live Statistics Dashboard
 * Shows processing speed, cost, time, and ETA
 * @version 3.0.0
 */
import { useState, useEffect } from '@wordpress/element';
import './LiveStats.css';

const LiveStats = ({ batchProgress }) => {
	const [elapsedTime, setElapsedTime] = useState(0);
	const [avgSpeed, setAvgSpeed] = useState(0);
	const [eta, setEta] = useState(0);

	// Update elapsed time every second
	useEffect(() => {
		const interval = setInterval(() => {
			if (batchProgress.startTime) {
				const elapsed = Math.floor((Date.now() - batchProgress.startTime) / 1000);
				setElapsedTime(elapsed);

				// Calculate average speed (images per second)
				if (elapsed > 0 && batchProgress.current > 0) {
					const speed = batchProgress.current / elapsed;
					setAvgSpeed(speed);

					// Calculate ETA
					const remaining = batchProgress.total - batchProgress.current;
					const etaSeconds = remaining / speed;
					setEta(etaSeconds);
				}
			}
		}, 1000);

		return () => clearInterval(interval);
	}, [batchProgress.startTime, batchProgress.current, batchProgress.total]);

	const formatTime = (seconds) => {
		if (seconds < 60) return `${Math.round(seconds)}s`;
		const mins = Math.floor(seconds / 60);
		const secs = Math.round(seconds % 60);
		if (mins < 60) return `${mins}:${secs.toString().padStart(2, '0')}`;
		const hours = Math.floor(mins / 60);
		const remainingMins = mins % 60;
		return `${hours}:${remainingMins.toString().padStart(2, '0')}:${secs.toString().padStart(2, '0')}`;
	};

	const avgCostPerImage = batchProgress.current > 0
		? batchProgress.totalCost / batchProgress.current
		: 0;

	return (
		<div className="live-stats-dashboard">
			<div className="stats-grid">
				{/* Processing Speed */}
				<div className="stat-card">
					<div className="stat-icon"></div>
					<div className="stat-content">
						<div className="stat-value">{avgSpeed.toFixed(2)}</div>
						<div className="stat-label">img/sec</div>
					</div>
				</div>

				{/* Total Cost */}
				<div className="stat-card">
					<div className="stat-icon"></div>
					<div className="stat-content">
						<div className="stat-value">${batchProgress.totalCost.toFixed(4)}</div>
						<div className="stat-label">Total Cost</div>
						<div className="stat-sublabel">${avgCostPerImage.toFixed(5)}/img</div>
					</div>
				</div>

				{/* Time Elapsed */}
				<div className="stat-card">
					<div className="stat-icon"></div>
					<div className="stat-content">
						<div className="stat-value">{formatTime(elapsedTime)}</div>
						<div className="stat-label">Elapsed</div>
					</div>
				</div>

				{/* ETA */}
				<div className="stat-card">
					<div className="stat-icon"></div>
					<div className="stat-content">
						<div className="stat-value">{formatTime(eta)}</div>
						<div className="stat-label">ETA</div>
					</div>
				</div>
			</div>
		</div>
	);
};

export default LiveStats;
