/**
 * Actions Cell
 * Displays Analyze button with processing state and animated stripes
 */

import { Button } from '@wordpress/components';

/**
 * ActionsCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object
 * @param {Object} props.queueStatus - Queue status object
 * @param {Array} props.processingIds - Array of processing attachment IDs
 * @param {Function} props.onAnalyze - Callback when Analyze button is clicked
 * @returns {JSX.Element} Rendered actions cell
 */
const ActionsCell = ({ attachment, queueStatus, processingIds, onAnalyze }) => {
	const status = attachment.meta?._ai_media_status || 'pending';
	const inQueue = queueStatus[attachment.id]?.in_queue || false;
	const isManuallyProcessing = processingIds.includes(attachment.id);
	const isProcessing = inQueue || isManuallyProcessing;

	return (
		<td>
			<Button
				variant="secondary"
				size="small"
				onClick={() => onAnalyze(attachment.id)}
				disabled={isProcessing}
				className={isProcessing ? 'analyze-button-processing' : ''}
			>
				Analyze
			</Button>
		</td>
	);
};

export default ActionsCell;
