/**
 * Pagination Component
 * Pagination controls for table navigation with progressive loading support
 */

import { Button, Spinner } from '@wordpress/components';
import { useState, useRef, useEffect } from '@wordpress/element';

/**
 * Pagination component
 * @param {Object} props - Component props
 * @param {number} props.currentPage - Current page number
 * @param {number|null} props.totalPages - Total number of pages (null = loading)
 * @param {number|null} props.totalItems - Total number of items (null = loading)
 * @param {boolean} props.countLoading - True if COUNT is loading in background
 * @param {Function} props.onPageChange - Callback when page changes
 * @returns {JSX.Element} Rendered pagination controls
 */
const Pagination = ({ currentPage, totalPages, totalItems, countLoading, onPageChange }) => {
	// Jump-to-page state
	const [isEditing, setIsEditing] = useState(false);
	const [pageInputValue, setPageInputValue] = useState(currentPage.toString());
	const inputRef = useRef(null);

	// Auto-focus and select text when entering edit mode
	useEffect(() => {
		if (isEditing && inputRef.current) {
			inputRef.current.focus();
			inputRef.current.select();
		}
	}, [isEditing]);

	// Handle click on page number - enter edit mode
	const handlePageNumberClick = () => {
		if (!countLoading) {
			setPageInputValue(currentPage.toString());
			setIsEditing(true);
		}
	};

	// Handle input change - only allow numbers
	const handleInputChange = (e) => {
		const value = e.target.value;
		// Allow only numbers
		if (value === '' || /^\d+$/.test(value)) {
			setPageInputValue(value);
		}
	};

	// Handle Enter and Escape keys
	const handleKeyDown = (e) => {
		if (e.key === 'Enter') {
			handleSubmit();
		} else if (e.key === 'Escape') {
			setIsEditing(false);
			setPageInputValue(currentPage.toString());
		}
	};

	// Handle blur - cancel edit mode
	const handleBlur = () => {
		setIsEditing(false);
		setPageInputValue(currentPage.toString());
	};

	// Submit page change with validation
	const handleSubmit = () => {
		const pageNumber = parseInt(pageInputValue, 10);

		// Validate range
		if (isNaN(pageNumber) || pageNumber < 1 || pageNumber > totalPages) {
			// Invalid input - reset and cancel
			setIsEditing(false);
			setPageInputValue(currentPage.toString());
			return;
		}

		// Valid page number - navigate
		setIsEditing(false);
		if (pageNumber !== currentPage) {
			onPageChange(pageNumber);
		}
	};
	// If COUNT is still loading (progressive loading), show placeholder
	if (totalItems === null || totalPages === null) {
		return (
			<div className="tablenav-pages pagination-loading">
				<Spinner />
				<span className="pagination-loading-text">
					{countLoading ? 'Counting images...' : 'Loading count...'}
				</span>
				<span className="pagination-hint">
					You can browse and edit images while counting
				</span>
			</div>
		);
	}

	if (totalPages <= 1) return null;

	// Disable navigation buttons while COUNT is loading (progressive loading safety)
	const navigationDisabled = countLoading;

	return (
		<div className="tablenav-pages">
			<span className="displaying-num">
				{totalItems} {totalItems === 1 ? 'item' : 'items'}
			</span>
			<span className="pagination-links">
				<Button
					className="first-page button"
					disabled={currentPage === 1 || navigationDisabled}
					onClick={() => onPageChange(1)}
					aria-label="First page"
					title={navigationDisabled ? 'Please wait while counting...' : ''}
				>
					
				</Button>
				<Button
					className="prev-page button"
					disabled={currentPage === 1 || navigationDisabled}
					onClick={() => onPageChange(currentPage - 1)}
					aria-label="Previous page"
					title={navigationDisabled ? 'Please wait while counting...' : ''}
				>
					
				</Button>
				<span className="paging-input">
					{isEditing ? (
						<span className="tablenav-paging-text">
							<input
								ref={inputRef}
								type="text"
								className="current-page"
								value={pageInputValue}
								onChange={handleInputChange}
								onKeyDown={handleKeyDown}
								onBlur={handleBlur}
								size="3"
								aria-label="Current page"
								aria-describedby="table-paging"
								disabled={countLoading}
							/>
							{' of '}
							<span className="total-pages">{totalPages}</span>
						</span>
					) : (
						<span
							className="tablenav-paging-text"
							onClick={handlePageNumberClick}
							style={{ cursor: countLoading ? 'default' : 'pointer', userSelect: 'none' }}
							title={countLoading ? '' : 'Click to jump to page'}
							role="button"
							tabIndex={countLoading ? -1 : 0}
							onKeyDown={(e) => {
								if (e.key === 'Enter' || e.key === ' ') {
									e.preventDefault();
									handlePageNumberClick();
								}
							}}
						>
							{currentPage} of <span className="total-pages">{totalPages}</span>
						</span>
					)}
				</span>
				<Button
					className="next-page button"
					disabled={currentPage === totalPages || navigationDisabled}
					onClick={() => onPageChange(currentPage + 1)}
					aria-label="Next page"
					title={navigationDisabled ? 'Please wait while counting...' : ''}
				>
					
				</Button>
				<Button
					className="last-page button"
					disabled={currentPage === totalPages || navigationDisabled}
					onClick={() => onPageChange(totalPages)}
					aria-label="Last page"
					title={navigationDisabled ? 'Please wait while counting...' : ''}
				>
					
				</Button>
			</span>
		</div>
	);
};

export default Pagination;
