	[STATUS_VALUES.PROCESSING]: 'ai-media-status-processing',
	[STATUS_VALUES.PROCESSED]: 'ai-media-status-processed',
	[STATUS_VALUES.APPROVED]: 'ai-media-status-approved',
	[STATUS_VALUES.FAILED]: 'ai-media-status-failed',
