/**
 * Get random processing status message
 * @returns {string} Random status message
 */
export const getProcessingStatusMessage = () => {
	const messages = [
		'Analyzing image...',
		'Processing with AI...',
		'Generating metadata...',
		'Optimizing for SEO...',
		'Almost there...',
	];
	return messages[Math.floor(Math.random() * messages.length)];
};
