/**
 * Thumbnail Preview component
 * Shows a larger preview of image when hovering over thumbnails
 */

import { useState } from '@wordpress/element';

/**
 * ThumbnailPreview component
 * @param {Object} props - Component props
 * @param {Object|null} props.hoveredImage - Hovered image data
 * @param {number} props.hoveredImage.id - Attachment ID
 * @param {string} props.hoveredImage.imageUrl - Image URL to display
 * @param {number} props.hoveredImage.x - X position for preview
 * @param {number} props.hoveredImage.y - Y position for preview
 * @returns {JSX.Element|null} Rendered preview or null if no hovered image
 */
const ThumbnailPreview = ({ hoveredImage }) => {
	const [imageError, setImageError] = useState(false);

	if (!hoveredImage) {
		return null;
	}

	// Handle image load error (404, etc.)
	const handleImageError = () => {
		setImageError(true);
	};

	return (
		<div
			className="ai-media-thumbnail-hover"
			style={{
				left: `${hoveredImage.x}px`,
				top: `${hoveredImage.y - 150}px`
			}}
		>
			{!imageError ? (
				<img
					src={hoveredImage.imageUrl}
					alt="Preview"
					onError={handleImageError}
				/>
			) : (
				<div
					style={{
						padding: '20px',
						background: '#f5f5f5',
						border: '1px solid #ddd',
						borderRadius: '4px',
						color: '#999',
						textAlign: 'center'
					}}
				>
					Image not available
				</div>
			)}
		</div>
	);
};

export default ThumbnailPreview;
