/**
 * Score Cell
 * Displays AI analysis score with color coding based on auto-approve threshold
 */

/**
 * Get score color based on auto-approve threshold
 * @param {number} score - Quality score (0.0-1.0)
 * @param {number} autoApproveThreshold - Auto-approve threshold (0.0-1.0)
 * @returns {Object} Color configuration
 */
const getScoreColor = (score, autoApproveThreshold = 0.85) => {
	const MINIMUM_QUALITY = 0.70; // Fixed minimum for acceptable quality

	if (score >= autoApproveThreshold) {
		return {
			className: 'score-high',
			bgColor: '#d4edda',
			textColor: '#155724',
		};
	} else if (score >= MINIMUM_QUALITY) {
		return {
			className: 'score-medium',
			bgColor: '#fff3cd',
			textColor: '#856404',
		};
	} else {
		return {
			className: 'score-low',
			bgColor: '#f8d7da',
			textColor: '#721c24',
		};
	}
};

/**
 * ScoreCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object
 * @returns {JSX.Element} Rendered score cell
 */
const ScoreCell = ({ attachment }) => {
	// Get auto-approve threshold from global settings
	const autoApproveThreshold = window.aiMediaSEO?.settings?.auto_approve_threshold || 0.85;

	return (
		<td>
			{attachment.score !== null && attachment.score !== undefined ? (
				(() => {
					const colors = getScoreColor(attachment.score, autoApproveThreshold);
					return (
						<span
							className={colors.className}
							style={{
								display: 'inline-block',
								padding: '4px 8px',
								borderRadius: '3px',
								fontSize: '13px',
								fontWeight: '600',
								backgroundColor: colors.bgColor,
								color: colors.textColor,
							}}
						>
							{(attachment.score * 100).toFixed(0)}%
						</span>
					);
				})()
			) : (
				<span style={{ color: '#999' }}></span>
			)}
		</td>
	);
};

export default ScoreCell;
