/**
 * Attached To Cell
 * Displays post/page attachment with attach/detach functionality
 */

/**
 * AttachedToCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object (includes parent_info from server)
 * @param {number|null} props.attachingTo - ID of attachment being attached (for loading state)
 * @param {Function} props.onAttachToPost - Callback to attach to post
 * @param {Function} props.onDetach - Callback to detach from post
 * @returns {JSX.Element} Rendered attached to cell
 */
const AttachedToCell = ({ attachment, attachingTo, onAttachToPost, onDetach }) => {
	const parentId = attachment.post || attachment.parent || 0;
	const parentPost = attachment.parent_info; // Server now returns parent_info directly

	if (attachingTo === attachment.id) {
		return (
			<td>
				<span style={{ color: '#999', fontSize: '13px' }}>Attaching...</span>
			</td>
		);
	}

	if (!parentId || parentId === 0) {
		// Not attached to any post
		return (
			<td>
				<button
					type="button"
					className="button-link"
					onClick={() => onAttachToPost(attachment.id)}
					style={{
						color: '#2271b1',
						textDecoration: 'none',
						background: 'none',
						border: 'none',
						padding: 0,
						cursor: 'pointer',
						fontSize: '13px',
						fontFamily: 'inherit',
					}}
				>
					Attach to post/page
				</button>
			</td>
		);
	}

	// Attached to a post/page
	if (parentPost) {
		// Format post type for display
		const getPostTypeLabel = (type) => {
			const typeLabels = {
				'post': 'Post',
				'page': 'Page',
			};
			return typeLabels[type] || type.replace(/_/g, ' ').replace(/\b\w/g, l => l.toUpperCase());
		};

		const adminUrl = window.aiMediaSEO?.adminUrl || '/wp-admin/';

		return (
			<td>
				<div style={{ fontSize: '13px' }}>
					<a
						href={`${adminUrl}post.php?post=${parentPost.id}&action=edit`}
						target="_blank"
						rel="noopener noreferrer"
						style={{
							color: '#2271b1',
							textDecoration: 'none',
						}}
					>
						{parentPost.title}
					</a>
					{parentPost.type && (
						<span style={{ color: '#646970', fontSize: '12px', marginLeft: '6px' }}>
							({getPostTypeLabel(parentPost.type)})
						</span>
					)}
					<br />
					<button
						type="button"
						className="button-link"
						onClick={() => onDetach(attachment.id)}
						style={{
							color: '#b32d2e',
							textDecoration: 'none',
							background: 'none',
							border: 'none',
							padding: 0,
							cursor: 'pointer',
							fontSize: '12px',
							marginTop: '4px',
							fontFamily: 'inherit',
						}}
					>
						Detach
					</button>
				</div>
			</td>
		);
	}

	// Parent ID exists but not found in parentPosts (loading or error)
	return (
		<td>
			<div style={{ fontSize: '13px', color: '#666' }}>
				Post #{parentId}
				<br />
				<button
					type="button"
					className="button-link"
					onClick={() => onDetach(attachment.id)}
					style={{
						color: '#b32d2e',
						textDecoration: 'none',
						background: 'none',
						border: 'none',
						padding: 0,
						cursor: 'pointer',
						fontSize: '12px',
						marginTop: '4px',
						fontFamily: 'inherit',
					}}
				>
					Detach
				</button>
			</div>
		</td>
	);
};

export default AttachedToCell;
