/**
 * Hook for polling queue status and tracking processing attachments
 */

import { useState, useEffect, useRef } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

/**
 * Poll queue status for processing attachments
 * @param {Array} allAttachments - All attachments
 * @param {Function} onRefreshAttachment - Callback to refresh an attachment
 * @returns {Object} Queue status state and utilities
 */
export const useQueueStatus = (allAttachments = [], onRefreshAttachment = null) => {
	const [queueStatus, setQueueStatus] = useState({}); // {attachmentId: {in_queue: bool, status: string}}
	const [processingIds, setProcessingIds] = useState([]);
	const pollingIntervalRef = useRef(null); // Track polling interval
	const refreshedAttachmentsRef = useRef(new Set()); // Track which attachments were already refreshed

	// Poll queue status and update processing attachments
	useEffect(() => {
		// Function to check queue and refresh finished attachments
		const checkQueueStatus = async () => {
			// Get current attachments that might be processing
			const currentAttachments = allAttachments.filter(att => {
				const status = att.meta?._ai_media_status || 'pending';
				return status === 'pending' || status === 'processing';
			});

			if (currentAttachments.length === 0) {
				// No processing attachments, clear any existing interval
				if (pollingIntervalRef.current) {
					clearInterval(pollingIntervalRef.current);
					pollingIntervalRef.current = null;
				}
				return;
			}

			try {
				const attachmentIds = currentAttachments.map(att => att.id).join(',');
				const response = await apiFetch({
					path: `/ai-media/v1/queue-status?attachment_ids=${attachmentIds}`,
					method: 'GET',
				});

				setQueueStatus(response);

				// Refresh attachments that finished processing (no longer in queue)
				const refreshPromises = [];
				for (const att of currentAttachments) {
					const currentStatus = att.meta?._ai_media_status;
					const queueInfo = response[att.id];

					// If attachment was processing but is no longer in queue, refresh it
					// Only refresh once per attachment (tracked in ref)
					if ((currentStatus === 'processing' || currentStatus === 'pending') &&
					    !queueInfo?.in_queue &&
					    !refreshedAttachmentsRef.current.has(att.id)) {
						refreshedAttachmentsRef.current.add(att.id);
						if (onRefreshAttachment) {
							refreshPromises.push(onRefreshAttachment(att.id));
						}
					}
				}

				// Wait for all refreshes to complete
				if (refreshPromises.length > 0) {
					await Promise.all(refreshPromises);
				}
			} catch (err) {
				// Silent error - polling will retry
			}
		};

		// Start polling if there are processing attachments
		const processingCount = allAttachments.filter(att => {
			const status = att.meta?._ai_media_status || 'pending';
			return status === 'pending' || status === 'processing';
		}).length;

		if (processingCount > 0) {
			// Check immediately
			checkQueueStatus();

			// Start interval if not already running
			if (!pollingIntervalRef.current) {
				pollingIntervalRef.current = setInterval(checkQueueStatus, 3000);
			}
		} else {
			// Stop polling if no processing attachments
			if (pollingIntervalRef.current) {
				clearInterval(pollingIntervalRef.current);
				pollingIntervalRef.current = null;
			}
			// Clear refreshed tracking when no more processing
			refreshedAttachmentsRef.current.clear();
		}

		// Cleanup on unmount
		return () => {
			if (pollingIntervalRef.current) {
				clearInterval(pollingIntervalRef.current);
				pollingIntervalRef.current = null;
			}
		};
	}, [allAttachments, onRefreshAttachment]);

	/**
	 * Add attachment ID to processing list
	 * @param {number} id - Attachment ID
	 */
	const addProcessingId = (id) => {
		setProcessingIds(prev => [...prev, id]);
	};

	/**
	 * Remove attachment ID from processing list
	 * @param {number} id - Attachment ID
	 */
	const removeProcessingId = (id) => {
		setProcessingIds(prev => prev.filter(prevId => prevId !== id));
	};

	return {
		queueStatus,
		processingIds,
		addProcessingId,
		removeProcessingId,
	};
};
