/**
 * Hook for managing thumbnail preview on hover
 */

import { useState } from '@wordpress/element';

/**
 * Hook for thumbnail hover preview
 * @returns {Object} Thumbnail preview state and handlers
 */
export const useThumbnailPreview = () => {
	const [hoveredImage, setHoveredImage] = useState(null);

	/**
	 * Handle thumbnail mouse enter
	 * @param {MouseEvent} e - Mouse event
	 * @param {Object} attachment - Attachment object
	 */
	const handleThumbnailEnter = (e, attachment) => {
		const rect = e.currentTarget.getBoundingClientRect();
		setHoveredImage({
			id: attachment.id,
			imageUrl: attachment.source_url,
			x: rect.right + 10,
			y: rect.top,
		});
	};

	/**
	 * Handle thumbnail mouse leave
	 */
	const handleThumbnailLeave = () => {
		setHoveredImage(null);
	};

	return {
		hoveredImage,
		handleThumbnailEnter,
		handleThumbnailLeave,
	};
};
