/**
 * File Cell
 * Displays filename and title with edit functionality and row actions
 */

import apiFetch from '@wordpress/api-fetch';
import { stripHtml } from '../../utils/formatters';
import { isAttachmentProcessing } from '../../utils/attachmentHelpers';

/**
 * FileCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object
 * @param {Object} props.editingTitle - Editing state {id, value}
 * @param {boolean} props.savingTitle - Whether save is in progress
 * @param {Object} props.queueStatus - Queue status object
 * @param {Function} props.onTitleClick - Callback when title is clicked
 * @param {Function} props.onTitleChange - Callback when title changes
 * @param {Function} props.onTitleKeyDown - Callback for key down events
 * @param {Function} props.onTitleSave - Callback to save title
 * @param {Function} props.onLoadAllAttachments - Callback to reload attachments after delete
 * @returns {JSX.Element} Rendered file cell
 */
const FileCell = ({
	attachment,
	editingTitle,
	savingTitle,
	queueStatus,
	onTitleClick,
	onTitleChange,
	onTitleKeyDown,
	onTitleSave,
	onLoadAllAttachments,
}) => {
	const isDraft = attachment.meta?._ai_media_status === 'processed';
	const draftTitle = attachment.meta?._ai_media_draft_title || '';
	const actualTitle = stripHtml(attachment.title?.rendered) || '';
	const displayTitle = isDraft && draftTitle ? draftTitle : actualTitle;
	const isProcessing = isAttachmentProcessing(attachment, queueStatus);

	const handleDelete = async (e) => {
		e.preventDefault();
		e.stopPropagation();
		if (!confirm(`Are you sure you want to delete "${stripHtml(attachment.title?.rendered) || 'Untitled'}" permanently?`)) {
			return;
		}
		try {
			await apiFetch({
				path: `/wp/v2/media/${attachment.id}?force=true`,
				method: 'DELETE',
			});
			onLoadAllAttachments();
		} catch (error) {
			alert('Error deleting image: ' + error.message);
		}
	};

	const handleCopyURL = (e) => {
		e.stopPropagation();
		navigator.clipboard.writeText(attachment.source_url);
		const span = e.target.nextElementSibling;
		if (span) {
			span.classList.remove('hidden');
			setTimeout(() => span.classList.add('hidden'), 2000);
		}
	};

	return (
		<td>
			{editingTitle && editingTitle.id === attachment.id ? (
				<input
					type="text"
					value={editingTitle.value}
					onChange={(e) => onTitleChange(e.target.value)}
					onKeyDown={onTitleKeyDown}
					onBlur={onTitleSave}
					disabled={savingTitle}
					autoFocus
					onClick={(e) => e.stopPropagation()}
					style={{
						width: '100%',
						padding: '4px 8px',
						border: '1px solid #8c8f94',
						borderRadius: '2px',
						fontSize: '13px',
						fontWeight: 'normal'
					}}
				/>
			) : (
				<div>
					<strong
						style={{ cursor: isProcessing ? 'not-allowed' : 'pointer' }}
						onClick={() => !isProcessing && onTitleClick(attachment)}
					>
						{/* Language flag emoji */}
						{attachment.language_info?.flag && (
							<span
								title={`${attachment.language_info.name || attachment.language_info.code} (${attachment.language_info.code})`}
								style={{
									fontSize: '16px',
									marginRight: '6px',
									verticalAlign: 'middle',
									display: 'inline-block',
									lineHeight: '1'
								}}
							>
								{attachment.language_info.flag}
							</span>
						)}
						{!displayTitle ? (
							<em style={{ color: '#999', fontWeight: 'normal' }}>Click to add title</em>
						) : isDraft && draftTitle ? (
							<span style={{
								backgroundColor: '#fff3cd',
								padding: '2px 4px',
								borderRadius: '2px',
								display: 'inline-block'
							}}>
								<strong style={{ color: '#856404', fontSize: '11px', marginRight: '4px' }}>DRAFT:</strong>
								{displayTitle}
							</span>
						) : (
							displayTitle || 'Untitled'
						)}
					</strong>
					<br />
					<small>{attachment.source_url?.split('/').pop() || ''}</small>
					<div className="row-actions">
						<span className="edit">
							<a href={`${window.aiMediaSEO?.adminUrl || '/wp-admin/'}post.php?post=${attachment.id}&action=edit`} aria-label={`Edit "${stripHtml(attachment.title?.rendered) || 'Untitled'}"`}>
								Edit
							</a> |
						</span>
						<span className="delete">
							<button
								type="button"
								className="button-link submitdelete"
								onClick={handleDelete}
								aria-label={`Delete "${stripHtml(attachment.title?.rendered) || 'Untitled'}" permanently`}
							>
								Delete
							</button> |
						</span>
						<span className="view">
							<a
								href={attachment.source_url}
								target="_blank"
								rel="noopener noreferrer"
								aria-label={`View "${stripHtml(attachment.title?.rendered) || 'Untitled'}"`}
							>
								View
							</a> |
						</span>
						<span className="copy">
							<button
								type="button"
								className="button-link copy-attachment-url"
								onClick={handleCopyURL}
								aria-label={`Copy "${stripHtml(attachment.title?.rendered) || 'Untitled'}" URL to clipboard`}
							>
								Copy URL
							</button>
							<span className="success hidden" aria-hidden="true">Copied!</span> |
						</span>
						<span className="download">
							<a
								href={attachment.source_url}
								aria-label={`Download "${stripHtml(attachment.title?.rendered) || 'Untitled'}"`}
								download
							>
								Download
							</a>
						</span>
					</div>
				</div>
			)}
		</td>
	);
};

export default FileCell;
