/**
 * Hook for managing attachment to post/page modal
 */

import { useState } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { stripHtml } from '../utils/formatters';

/**
 * Hook for attach modal state and handlers
 * @param {Function} attachToParent - Function to attach image to parent (from useAttachments)
 * @returns {Object} Attach modal state and handlers
 */
export const useAttachModal = (attachToParent) => {
	const [isOpen, setIsOpen] = useState(false);
	const [search, setSearch] = useState('');
	const [results, setResults] = useState([]);
	const [selected, setSelected] = useState(null);
	const [searching, setSearching] = useState(false);
	const [attachmentId, setAttachmentId] = useState(null);

	/**
	 * Load recent posts, pages and custom post types
	 */
	const loadRecentContent = async () => {
		setSearching(true);
		const allResults = [];

		try {
			// Load posts
			const posts = await apiFetch({
				path: `/wp/v2/posts?per_page=100&orderby=date&order=desc`,
				method: 'GET',
			});
			allResults.push(...posts.map(post => ({
				id: post.id,
				type: 'post',
				typeName: 'Posts',
				title: stripHtml(post.title?.rendered) || 'Untitled',
				date: post.date,
				status: post.status,
			})));
		} catch (e) {
			// Error loading posts
		}

		try {
			// Load pages
			const pages = await apiFetch({
				path: `/wp/v2/pages?per_page=100&orderby=date&order=desc`,
				method: 'GET',
			});
			allResults.push(...pages.map(page => ({
				id: page.id,
				type: 'page',
				typeName: 'Pages',
				title: stripHtml(page.title?.rendered) || 'Untitled',
				date: page.date,
				status: page.status,
			})));
		} catch (e) {
			// Error loading pages
		}

		// Try to load custom post types
		try {
			const typesResponse = await apiFetch({
				path: `/wp/v2/types`,
				method: 'GET',
			});

			// Get custom post types (not posts, pages, attachments, navigation, etc.)
			const customTypes = Object.entries(typesResponse)
				.filter(([key, type]) => {
					return !['post', 'page', 'attachment', 'wp_block', 'wp_template', 'wp_template_part', 'wp_navigation', 'nav_menu_item'].includes(key) &&
						   type.rest_base;
				});

			// Try to load items from each custom type
			for (const [key, type] of customTypes) {
				try {
					const items = await apiFetch({
						path: `/wp/v2/${type.rest_base}?per_page=100&orderby=date&order=desc`,
						method: 'GET',
					});

					allResults.push(...items.map(item => ({
						id: item.id,
						type: key,
						typeName: type.name || key,
						title: stripHtml(item.title?.rendered) || 'Untitled',
						date: item.date,
						status: item.status,
					})));
				} catch (err) {
					// Could not load custom post type
				}
			}
		} catch (err) {
			// Error loading custom post types
		}

		// Sort by date and show all results (no limit)
		const sorted = allResults.sort((a, b) => new Date(b.date) - new Date(a.date));
		setResults(sorted);
		setSearching(false);
	};

	/**
	 * Open modal for specific attachment
	 * @param {number} id - Attachment ID
	 */
	const handleOpen = async (id) => {
		setAttachmentId(id);
		setIsOpen(true);
		setSearch('');
		setResults([]);
		setSelected(null);
		// Load recent content automatically
		await loadRecentContent();
	};

	/**
	 * Close modal and reset state
	 */
	const handleClose = () => {
		setIsOpen(false);
		setSelected(null);
		setAttachmentId(null);
	};

	/**
	 * Search for posts, pages and custom post types
	 */
	const handleSearch = async () => {
		if (!search.trim()) {
			// If search is empty, reload recent content
			await loadRecentContent();
			return;
		}

		setSearching(true);
		const searchResults = [];

		try {
			// Search in posts
			const posts = await apiFetch({
				path: `/wp/v2/posts?search=${encodeURIComponent(search)}&per_page=100`,
				method: 'GET',
			});
			searchResults.push(...posts.map(post => ({
				id: post.id,
				type: 'post',
				typeName: 'Posts',
				title: stripHtml(post.title?.rendered) || 'Untitled',
				date: post.date,
				status: post.status,
			})));
		} catch (e) {
			// Error searching posts
		}

		try {
			// Search in pages
			const pages = await apiFetch({
				path: `/wp/v2/pages?search=${encodeURIComponent(search)}&per_page=100`,
				method: 'GET',
			});
			searchResults.push(...pages.map(page => ({
				id: page.id,
				type: 'page',
				typeName: 'Pages',
				title: stripHtml(page.title?.rendered) || 'Untitled',
				date: page.date,
				status: page.status,
			})));
		} catch (e) {
			// Error searching pages
		}

		// Search in custom post types
		try {
			const typesResponse = await apiFetch({
				path: `/wp/v2/types`,
				method: 'GET',
			});

			const customTypes = Object.entries(typesResponse)
				.filter(([key, type]) => {
					return !['post', 'page', 'attachment', 'wp_block', 'wp_template', 'wp_template_part', 'wp_navigation', 'nav_menu_item'].includes(key) &&
						   type.rest_base;
				});

			for (const [key, type] of customTypes) {
				try {
					const items = await apiFetch({
						path: `/wp/v2/${type.rest_base}?search=${encodeURIComponent(search)}&per_page=100`,
						method: 'GET',
					});

					searchResults.push(...items.map(item => ({
						id: item.id,
						type: key,
						typeName: type.name || key,
						title: stripHtml(item.title?.rendered) || 'Untitled',
						date: item.date,
						status: item.status,
					})));
				} catch (err) {
					// Could not search in custom post type
				}
			}
		} catch (err) {
			// Error searching custom post types
		}

		// Sort by date
		const sorted = searchResults.sort((a, b) => new Date(b.date) - new Date(a.date));
		setResults(sorted);
		setSearching(false);
	};

	/**
	 * Clear search and reload recent content
	 */
	const handleClearSearch = async () => {
		setSearch('');
		await loadRecentContent();
	};

	/**
	 * Confirm attachment to selected post/page
	 */
	const handleConfirm = async () => {
		if (!selected || !attachmentId) return;

		try {
			await attachToParent(attachmentId, selected.id);
			handleClose();
		} catch (err) {
			alert('Error attaching image: ' + err.message);
		}
	};

	return {
		isOpen,
		search,
		setSearch,
		results,
		selected,
		setSelected,
		searching,
		attachmentId,
		handleOpen,
		handleClose,
		handleSearch,
		handleClearSearch,
		handleConfirm,
	};
};
