/**
 * Media Library Table Component
 * Complete table with thead, tbody, and pagination
 */

import TableRow from './TableRow';
import Pagination from './Pagination';

/**
 * MediaLibraryTable component
 * @param {Object} props - Component props
 * @param {Array} props.paginatedAttachments - Array of attachments to display
 * @param {Array} props.selectedIds - Array of selected attachment IDs
 * @param {Function} props.onSelectAll - Callback to select/deselect all
 * @param {Function} props.onToggleSelection - Callback to toggle single selection
 * @param {Function} props.isColumnVisible - Function to check column visibility
 * @param {number} props.currentPage - Current page number
 * @param {number|null} props.totalPages - Total number of pages (null = loading)
 * @param {number|null} props.totalItems - Total number of items (null = loading)
 * @param {boolean} props.countLoading - True if COUNT is loading in background
 * @param {Function} props.onPageChange - Callback when page changes
 * @param {Object} props.editingTitle - Editing title state
 * @param {boolean} props.savingTitle - Whether title is being saved
 * @param {Object} props.editingAlt - Editing ALT state
 * @param {boolean} props.savingAlt - Whether ALT is being saved
 * @param {Object} props.editingCaption - Editing caption state
 * @param {boolean} props.savingCaption - Whether caption is being saved
 * @param {Object} props.editingStatus - Editing status state
 * @param {boolean} props.savingStatus - Whether status is being saved
 * @param {Object} props.queueStatus - Queue status object
 * @param {Array} props.processingIds - Array of processing attachment IDs
 * @param {number|null} props.attachingTo - Attachment ID being attached
 * @param {Function} props.onTitleClick - Title click handler
 * @param {Function} props.onTitleChange - Title change handler
 * @param {Function} props.onTitleKeyDown - Title key down handler
 * @param {Function} props.onTitleSave - Title save handler
 * @param {Function} props.onLoadAllAttachments - Reload attachments handler
 * @param {Function} props.onAltClick - ALT click handler
 * @param {Function} props.onAltChange - ALT change handler
 * @param {Function} props.onAltKeyDown - ALT key down handler
 * @param {Function} props.onAltSave - ALT save handler
 * @param {Function} props.onCaptionClick - Caption click handler
 * @param {Function} props.onCaptionChange - Caption change handler
 * @param {Function} props.onCaptionKeyDown - Caption key down handler
 * @param {Function} props.onCaptionSave - Caption save handler
 * @param {Function} props.onStatusClick - Status click handler
 * @param {Function} props.onStatusChange - Status change handler
 * @param {Function} props.onStatusSave - Status save handler
 * @param {Function} props.onApprove - Approve handler
 * @param {Function} props.onAttachToPost - Attach to post handler
 * @param {Function} props.onDetach - Detach handler
 * @param {Function} props.onAnalyze - Analyze handler
 * @param {Function} props.onThumbnailEnter - Thumbnail mouse enter handler
 * @param {Function} props.onThumbnailLeave - Thumbnail mouse leave handler
 * @returns {JSX.Element} Rendered table
 */
const MediaLibraryTable = ({
	paginatedAttachments,
	selectedIds,
	onSelectAll,
	onToggleSelection,
	isColumnVisible,
	currentPage,
	totalPages,
	totalItems,
	countLoading,
	onPageChange,
	editingTitle,
	savingTitle,
	editingAlt,
	savingAlt,
	editingCaption,
	savingCaption,
	editingStatus,
	savingStatus,
	queueStatus,
	processingIds,
	attachingTo,
	onTitleClick,
	onTitleChange,
	onTitleKeyDown,
	onTitleSave,
	onLoadAllAttachments,
	onAltClick,
	onAltChange,
	onAltKeyDown,
	onAltSave,
	onCaptionClick,
	onCaptionChange,
	onCaptionKeyDown,
	onCaptionSave,
	onStatusClick,
	onStatusChange,
	onStatusSave,
	onApprove,
	onAttachToPost,
	onDetach,
	onAnalyze,
	onThumbnailEnter,
	onThumbnailLeave,
}) => {
	// Calculate colspan for empty state
	const colspan = 3 +
		(isColumnVisible('file') ? 1 : 0) +
		(isColumnVisible('alt') ? 1 : 0) +
		(isColumnVisible('caption') ? 1 : 0) +
		(isColumnVisible('status') ? 1 : 0) +
		(isColumnVisible('attached_to') ? 1 : 0) +
		(isColumnVisible('provider') ? 1 : 0) +
		(isColumnVisible('score') ? 1 : 0);

	return (
		<div className="ai-media-library-table">
			{/* Pagination Controls - Top */}
			{(totalPages === null || totalPages > 1) && (
				<div className="tablenav top">
					<Pagination
						currentPage={currentPage}
						totalPages={totalPages}
						totalItems={totalItems}
						countLoading={countLoading}
						onPageChange={onPageChange}
					/>
				</div>
			)}

			<table className="wp-list-table widefat fixed striped">
				<thead>
					<tr>
						<th className="check-column" onClick={onSelectAll} style={{ cursor: 'pointer' }}>
							<input
								type="checkbox"
								checked={
									paginatedAttachments.length > 0 &&
									paginatedAttachments.every(att => selectedIds.includes(att.id))
								}
								onChange={(e) => e.stopPropagation()}
								onClick={onSelectAll}
							/>
						</th>
						<th className="thumbnail-column">Image</th>
						{isColumnVisible('file') && <th>File</th>}
						{isColumnVisible('alt') && <th>ALT text</th>}
						{isColumnVisible('caption') && <th>Caption</th>}
						{isColumnVisible('status') && <th>Status</th>}
						{isColumnVisible('attached_to') && <th>Attached to</th>}
						{isColumnVisible('provider') && <th>Model</th>}
						{isColumnVisible('score') && <th>Score</th>}
						<th>Actions</th>
					</tr>
				</thead>
				<tbody>
					{paginatedAttachments.length === 0 ? (
						<tr>
							<td colSpan={colspan} style={{ textAlign: 'center', padding: '2em' }}>
								No images found. Upload some images to get started.
							</td>
						</tr>
					) : (
						paginatedAttachments.map((attachment) => (
							<TableRow
								key={attachment.id}
								attachment={attachment}
								isSelected={selectedIds.includes(attachment.id)}
								onToggleSelection={onToggleSelection}
								isColumnVisible={isColumnVisible}
								editingTitle={editingTitle}
								savingTitle={savingTitle}
								editingAlt={editingAlt}
								savingAlt={savingAlt}
								editingCaption={editingCaption}
								savingCaption={savingCaption}
								editingStatus={editingStatus}
								savingStatus={savingStatus}
								queueStatus={queueStatus}
								processingIds={processingIds}
								attachingTo={attachingTo}
								onTitleClick={onTitleClick}
								onTitleChange={onTitleChange}
								onTitleKeyDown={onTitleKeyDown}
								onTitleSave={onTitleSave}
								onLoadAllAttachments={onLoadAllAttachments}
								onAltClick={onAltClick}
								onAltChange={onAltChange}
								onAltKeyDown={onAltKeyDown}
								onAltSave={onAltSave}
								onCaptionClick={onCaptionClick}
								onCaptionChange={onCaptionChange}
								onCaptionKeyDown={onCaptionKeyDown}
								onCaptionSave={onCaptionSave}
								onStatusClick={onStatusClick}
								onStatusChange={onStatusChange}
								onStatusSave={onStatusSave}
								onApprove={onApprove}
								onAttachToPost={onAttachToPost}
								onDetach={onDetach}
								onAnalyze={onAnalyze}
								onThumbnailEnter={onThumbnailEnter}
								onThumbnailLeave={onThumbnailLeave}
							/>
						))
					)}
				</tbody>
			</table>

			{/* Pagination Controls - Bottom */}
			{(totalPages === null || totalPages > 1) && (
				<div className="tablenav bottom">
					<Pagination
						currentPage={currentPage}
						totalPages={totalPages}
						totalItems={totalItems}
						countLoading={countLoading}
						onPageChange={onPageChange}
					/>
				</div>
			)}
		</div>
	);
};

export default MediaLibraryTable;
