/**
 * Cost Statistics Modal
 * Displays detailed cost statistics and breakdown by model
 */

import { Modal, Spinner } from '@wordpress/components';

/**
 * CostStatsModal component
 * @param {Object} props - Component props
 * @param {boolean} props.isOpen - Whether modal is open
 * @param {Function} props.onClose - Callback to close modal
 * @param {Object|null} props.costStats - Overall cost statistics
 * @param {number} props.costStats.total_cost - Total cost
 * @param {number} props.costStats.total_images_processed - Total images processed
 * @param {number} props.costStats.average_cost_per_image - Average cost per image
 * @param {number} props.costStats.total_tokens_used - Total tokens used
 * @param {string} props.costStats.most_expensive_model - Most expensive model
 * @param {string} props.costStats.last_sync - Last pricing update timestamp
 * @param {Object|null} props.costBreakdown - Cost breakdown by model
 * @param {Object} props.costBreakdown.by_model - Breakdown by model {model: {jobs, input_tokens, output_tokens, total_cost}}
 * @param {number} props.costBreakdown.total_jobs - Total jobs
 * @param {number} props.costBreakdown.total_cost - Total cost
 * @param {number} [props.liveTotalCost] - Live total cost from state (overrides costStats.total_cost if provided)
 * @returns {JSX.Element|null} Rendered modal or null if not open
 */
const CostStatsModal = ({ isOpen, onClose, costStats, costBreakdown, liveTotalCost }) => {
	if (!isOpen) {
		return null;
	}

	return (
		<Modal
			title="Cost Statistics"
			onRequestClose={onClose}
			style={{ maxWidth: '800px' }}
		>
			<div>
				{!costStats || !costBreakdown ? (
					<div style={{ textAlign: 'center', padding: '40px' }}>
						<Spinner />
					</div>
				) : (
					<>
						{/* Overall Stats */}
						<div style={{
							background: '#f0f6fc',
							padding: '20px',
							borderRadius: '8px',
							marginBottom: '24px',
							border: '1px solid #c3e7ff',
						}}>
							<h3 style={{
								margin: '0 0 16px 0',
								fontSize: '18px',
								color: '#1d2327',
								display: 'flex',
								alignItems: 'center',
								gap: '8px',
							}}>
								<span className="dashicons dashicons-chart-bar" style={{ color: '#2271b1' }}></span>
								Overall Statistics
							</h3>
							<div style={{ display: 'grid', gridTemplateColumns: 'repeat(3, 1fr)', gap: '16px' }}>
								<div>
									<div style={{ fontSize: '12px', color: '#646970', marginBottom: '4px' }}>
										Total Cost
									</div>
									<div style={{ fontSize: '24px', fontWeight: '600', color: '#2271b1' }}>
										${(liveTotalCost !== undefined ? liveTotalCost : costStats.total_cost).toFixed(4)}
									</div>
								</div>
								<div>
									<div style={{ fontSize: '12px', color: '#646970', marginBottom: '4px' }}>
										Images Processed
									</div>
									<div style={{ fontSize: '24px', fontWeight: '600', color: '#1d2327' }}>
										{costStats.total_images_processed.toLocaleString()}
									</div>
								</div>
								<div>
									<div style={{ fontSize: '12px', color: '#646970', marginBottom: '4px' }}>
										Avg Cost/Image
									</div>
									<div style={{ fontSize: '24px', fontWeight: '600', color: '#1d2327' }}>
										${costStats.average_cost_per_image.toFixed(6)}
									</div>
								</div>
							</div>
							<div style={{ display: 'grid', gridTemplateColumns: 'repeat(3, 1fr)', gap: '16px', marginTop: '16px' }}>
								<div>
									<div style={{ fontSize: '12px', color: '#646970', marginBottom: '4px' }}>
										Total Tokens Used
									</div>
									<div style={{ fontSize: '18px', fontWeight: '600', color: '#1d2327' }}>
										{costStats.total_tokens_used.toLocaleString()}
									</div>
								</div>
								<div style={{ gridColumn: '3' }}>
									<div style={{ fontSize: '12px', color: '#646970', marginBottom: '4px' }}>
										Most Expensive Model
									</div>
									<div style={{ fontSize: '18px', fontWeight: '600', color: '#1d2327' }}>
										{costStats.most_expensive_model || 'N/A'}
									</div>
								</div>
							</div>
							{costStats.last_sync && (
								<div style={{ marginTop: '16px', fontSize: '12px', color: '#646970' }}>
									Last pricing update: {costStats.last_sync}
								</div>
							)}
						</div>

						{/* Cost Breakdown by Model */}
						<div>
							<h3 style={{
								margin: '0 0 16px 0',
								fontSize: '18px',
								color: '#1d2327',
								display: 'flex',
								alignItems: 'center',
								gap: '8px',
							}}>
								<span className="dashicons dashicons-performance" style={{ color: '#2271b1' }}></span>
								Cost Breakdown by Model
							</h3>
							<table style={{
								width: '100%',
								borderCollapse: 'collapse',
								border: '1px solid #c3c4c7',
								background: '#fff',
							}}>
								<thead>
									<tr style={{ background: '#f6f7f7', borderBottom: '2px solid #c3c4c7' }}>
										<th style={{ padding: '12px', textAlign: 'left', fontSize: '13px', fontWeight: '600' }}>
											Model
										</th>
										<th style={{ padding: '12px', textAlign: 'right', fontSize: '13px', fontWeight: '600' }}>
											Images
										</th>
										<th style={{ padding: '12px', textAlign: 'right', fontSize: '13px', fontWeight: '600' }}>
											Input Tokens
										</th>
										<th style={{ padding: '12px', textAlign: 'right', fontSize: '13px', fontWeight: '600' }}>
											Output Tokens
										</th>
										<th style={{ padding: '12px', textAlign: 'right', fontSize: '13px', fontWeight: '600' }}>
											Total Cost
										</th>
									</tr>
								</thead>
								<tbody>
									{Object.keys(costBreakdown.by_model).length === 0 ? (
										<tr>
											<td colSpan="5" style={{ padding: '20px', textAlign: 'center', color: '#646970' }}>
												No cost data available yet. Process some images to see statistics.
											</td>
										</tr>
									) : (
										Object.entries(costBreakdown.by_model).map(([model, data]) => (
											<tr key={model} style={{ borderBottom: '1px solid #c3c4c7' }}>
												<td style={{ padding: '12px', fontSize: '13px', fontWeight: '500' }}>
													{model}
												</td>
												<td style={{ padding: '12px', textAlign: 'right', fontSize: '13px' }}>
													{data.jobs.toLocaleString()}
												</td>
												<td style={{ padding: '12px', textAlign: 'right', fontSize: '13px', color: '#646970' }}>
													{data.input_tokens.toLocaleString()}
												</td>
												<td style={{ padding: '12px', textAlign: 'right', fontSize: '13px', color: '#646970' }}>
													{data.output_tokens.toLocaleString()}
												</td>
												<td style={{ padding: '12px', textAlign: 'right', fontSize: '13px', fontWeight: '600', color: '#2271b1' }}>
													${data.total_cost.toFixed(6)}
												</td>
											</tr>
										))
									)}
								</tbody>
								{Object.keys(costBreakdown.by_model).length > 0 && (
									<tfoot>
										<tr style={{ background: '#f6f7f7', borderTop: '2px solid #c3c4c7' }}>
											<td style={{ padding: '12px', fontSize: '13px', fontWeight: '600' }}>
												Total
											</td>
											<td style={{ padding: '12px', textAlign: 'right', fontSize: '13px', fontWeight: '600' }}>
												{costBreakdown.total_jobs.toLocaleString()}
											</td>
											<td colSpan="2"></td>
											<td style={{ padding: '12px', textAlign: 'right', fontSize: '14px', fontWeight: '700', color: '#2271b1' }}>
												${costBreakdown.total_cost.toFixed(6)}
											</td>
										</tr>
									</tfoot>
								)}
							</table>
						</div>

						<div style={{
							marginTop: '20px',
							padding: '12px',
							background: '#f9f9f9',
							border: '1px solid #c3c4c7',
							borderRadius: '4px',
							fontSize: '12px',
							color: '#646970',
						}}>
							<strong>Note:</strong> Costs are calculated based on current model pricing and actual token usage.
							Pricing is updated daily from external sources.
						</div>
					</>
				)}
			</div>
		</Modal>
	);
};

export default CostStatsModal;
