/**
 * Upload Modal
 * Modal for uploading images via drag-and-drop or file selection
 */

import { Modal, Button, Spinner } from '@wordpress/components';

/**
 * UploadModal component
 * @param {Object} props - Component props
 * @param {boolean} props.isOpen - Whether modal is open
 * @param {Function} props.onClose - Callback to close modal
 * @param {Object} props.fileInputRef - Ref for file input element
 * @param {Function} props.onFileSelect - Callback when files are selected via input
 * @param {boolean} props.isDragging - Whether files are being dragged over drop zone
 * @param {Function} props.onDragEnter - Callback when drag enters drop zone
 * @param {Function} props.onDragLeave - Callback when drag leaves drop zone
 * @param {Function} props.onDragOver - Callback when dragging over drop zone
 * @param {Function} props.onDrop - Callback when files are dropped
 * @param {boolean} props.uploading - Whether upload is in progress
 * @param {Array} props.uploadFiles - Array of files to upload
 * @param {Function} props.onStartUpload - Callback to start upload
 * @param {Object} props.uploadProgress - Upload progress object {filename: {status, progress, id}}
 * @param {Function} props.onRemoveFile - Callback to remove file from upload list
 * @returns {JSX.Element|null} Rendered modal or null if not open
 */
const UploadModal = ({
	isOpen,
	onClose,
	fileInputRef,
	onFileSelect,
	isDragging,
	onDragEnter,
	onDragLeave,
	onDragOver,
	onDrop,
	uploading,
	uploadFiles,
	onStartUpload,
	uploadProgress,
	onRemoveFile,
}) => {
	if (!isOpen) {
		return null;
	}

	return (
		<Modal
			title="Drop files to upload"
			onRequestClose={onClose}
			className="ai-media-upload-modal"
		>
			<div className="upload-modal-content">
				{/* Hidden file input */}
				<input
					ref={fileInputRef}
					type="file"
					multiple
					accept="image/*"
					style={{ display: 'none' }}
					onChange={onFileSelect}
				/>

				{/* Drop Zone */}
				<div
					className={`upload-drop-zone ${isDragging ? 'dragging' : ''}`}
					onDragEnter={onDragEnter}
					onDragLeave={onDragLeave}
					onDragOver={onDragOver}
					onDrop={onDrop}
					onClick={() => !uploading && fileInputRef.current?.click()}
				>
					<div className="upload-drop-zone-content">
						<span className="dashicons dashicons-upload" style={{ fontSize: '80px', color: '#c3c4c7', marginBottom: '55px', marginLeft: '-55px' }}></span>
						<p style={{ fontSize: '18px', margin: '0 0 8px 0', color: '#1d2327' }}>
							{isDragging ? 'Drop files here' : 'Drop images here'}
						</p>
						<p style={{ fontSize: '14px', margin: '0 0 16px 0', color: '#646970' }}>
							or
						</p>
						<Button variant="primary" disabled={uploading}>
							Select Files
						</Button>
						<p style={{ fontSize: '13px', margin: '16px 0 0 0', color: '#646970', fontStyle: 'italic' }}>
							Maximum upload file size: 128 MB
						</p>
					</div>
				</div>

				{/* Upload Actions - Show when files selected */}
				{uploadFiles.length > 0 && (
					<div className="upload-modal-actions" style={{
						display: 'flex',
						justifyContent: 'center',
						gap: '12px',
						marginTop: '20px',
						paddingTop: '20px',
						borderTop: '1px solid #dcdcde'
					}}>
						<Button
							variant="secondary"
							onClick={onClose}
							disabled={uploading}
						>
							Close
						</Button>
						<Button
							variant="primary"
							onClick={onStartUpload}
							disabled={uploadFiles.length === 0 || uploading}
							isBusy={uploading}
						>
							{uploading ? 'Uploading...' : `Upload (${uploadFiles.length})`}
						</Button>
					</div>
				)}

				{/* File List - Show upload progress */}
				{uploadFiles.length > 0 && (
					<div className="upload-file-list">
						<h3 style={{ fontSize: '14px', fontWeight: '600', margin: '24px 0 12px 0', color: '#1d2327' }}>
							Upload Progress
						</h3>
						<div className="upload-files">
							{uploadFiles.map((file, index) => {
								const progress = uploadProgress[file.name] || { status: 'pending', progress: 0 };
								return (
									<div key={index} className="upload-file-item">
										<div className="upload-file-info">
											<span className="upload-file-name">{file.name}</span>
											<span className="upload-file-size">
												{(file.size / 1024 / 1024).toFixed(2)} MB
											</span>
										</div>
										<div className="upload-file-status">
											{progress.status === 'pending' && (
												<button
													type="button"
													className="upload-file-remove"
													onClick={() => onRemoveFile(file.name)}
													disabled={uploading}
												>
													<span className="dashicons dashicons-no-alt"></span>
												</button>
											)}
											{progress.status === 'uploading' && (
												<Spinner />
											)}
											{progress.status === 'completed' && (
												<span className="dashicons dashicons-yes-alt" style={{ color: '#00a32a', fontSize: '20px' }}></span>
											)}
											{progress.status === 'failed' && (
												<span className="dashicons dashicons-warning" style={{ color: '#d63638', fontSize: '20px' }} title={progress.error}></span>
											)}
										</div>
									</div>
								);
							})}
						</div>
					</div>
				)}
			</div>
		</Modal>
	);
};

export default UploadModal;
