/**
 * Interactive SelectControl with hover/focus effects
 * Provides a styled select dropdown with WordPress admin-like styling
 */

import { useState } from '@wordpress/element';

/**
 * Interactive SelectControl component
 * @param {Object} props - Component props
 * @param {string} props.label - Label for the select control
 * @param {string} props.value - Current selected value
 * @param {Array} props.options - Array of {label, value} option objects
 * @param {Function} props.onChange - Callback when selection changes
 * @returns {JSX.Element} Rendered select control
 */
const InteractiveSelectControl = ({ label, value, options, onChange }) => {
	const [isHovered, setIsHovered] = useState(false);
	const [isFocused, setIsFocused] = useState(false);

	const getSelectStyle = () => {
		const baseStyle = {
			minHeight: '38px',
			padding: '8px 32px 8px 12px',
			fontSize: '14px',
			lineHeight: '1.4',
			border: '1px solid #8c8f94',
			borderRadius: '3px',
			background: '#fff',
			color: '#1d2327',
			boxSizing: 'border-box',
			appearance: 'none',
			backgroundImage: "url('data:image/svg+xml;charset=US-ASCII,%3Csvg%20width%3D%2220%22%20height%3D%2220%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M5%206l5%205%205-5%202%201-7%207-7-7%202-1z%22%20fill%3D%22%232c3338%22%2F%3E%3C%2Fsvg%3E')",
			backgroundRepeat: 'no-repeat',
			backgroundPosition: 'right 8px top 50%',
			backgroundSize: '20px 20px',
			transition: 'all 0.2s ease',
			cursor: 'pointer',
			width: '100%',
		};

		if (isFocused) {
			return {
				...baseStyle,
				borderColor: '#2271b1',
				outline: '2px solid transparent',
				boxShadow: '0 0 0 1px #2271b1, 0 2px 4px rgba(34, 113, 177, 0.1)',
				backgroundColor: '#fff',
			};
		}

		if (isHovered) {
			return {
				...baseStyle,
				borderColor: '#2271b1',
				backgroundColor: '#f9f9f9',
			};
		}

		return baseStyle;
	};

	return (
		<div className="components-base-control">
			<label
				className="components-base-control__label"
				style={{
					display: 'block',
					marginBottom: '8px',
					fontSize: '14px',
					fontWeight: '600',
					color: '#1d2327',
					lineHeight: '1.4',
				}}
			>
				{label}
			</label>
			<select
				value={value}
				onChange={(e) => onChange(e.target.value)}
				onMouseEnter={() => setIsHovered(true)}
				onMouseLeave={() => setIsHovered(false)}
				onFocus={() => setIsFocused(true)}
				onBlur={() => setIsFocused(false)}
				style={getSelectStyle()}
			>
				{options.map((option) => (
					<option key={option.value} value={option.value}>
						{option.label}
					</option>
				))}
			</select>
		</div>
	);
};

export default InteractiveSelectControl;
