/**
 * Check if quality weights sum to 100% (with 1% tolerance for floating point)
 */
export const isWeightsSumValid = (qualityWeights) => {
	const total = qualityWeights.alt + qualityWeights.title +
		qualityWeights.caption + qualityWeights.keywords;
	return Math.abs(total - 1.0) <= 0.01;
};

/**
 * Calculate quality score from field scores and weights
 */
export const calculateQualityScore = (fieldScores, qualityWeights) => {
	let total = 0;
	const validFields = ['alt', 'title', 'caption', 'keywords'];
	for (const field of validFields) {
		if (fieldScores[field] !== undefined) {
			total += fieldScores[field] * (qualityWeights[field] || 0);
		}
	}
	return total;
};

/**
 * Check if current settings match a preset
 */
export const checkCurrentPreset = (settings, qualityWeights, presets) => {
	for (const [id, preset] of Object.entries(presets)) {
		const weightsMatch = Object.keys(preset.weights).every(
			key => Math.abs(qualityWeights[key] - preset.weights[key]) < 0.01
		);
		const thresholdMatch = Math.abs(
			(settings.auto_approve_threshold || 0.85) - preset.autoApprove
		) < 0.01;

		if (weightsMatch && thresholdMatch) {
			return id;
		}
	}
	return 'custom';
};
