/**
 * Hook for caching library pages and prefetching
 *
 * Implements LRU (Least Recently Used) cache for media library pages
 * with automatic prefetching of adjacent pages in background.
 */

import { useRef, useCallback } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

/**
 * Maximum number of pages to keep in cache
 * 20 pages = 400 items (at 20 per page) = ~20-40 MB RAM
 * Larger cache improves navigation speed for users browsing multiple pages
 */
const MAX_CACHED_PAGES = 20;

/**
 * Cache library pages and prefetch adjacent pages
 * @returns {Object} Cache functions
 */
export const useLibraryCache = () => {
	const cache = useRef(new Map()); // Map pro cache strnek
	const prefetchAbortController = useRef(null); // AbortController for prefetch requests
	const prefetchTimeout = useRef(null); // Timeout for debounced prefetch

	/**
	 * Detect current Polylang language from URL
	 *
	 * IMPORTANT: We ONLY use URL parameter, NOT cookie.
	 * Cookie would cause language filter to persist even when user navigates to Library
	 * without ?lang= parameter, which is unexpected behavior.
	 *
	 * Default behavior (no ?lang= parameter) = show ALL languages
	 *
	 * @returns {string|null} Language code or null (null = all languages)
	 */
	const detectPolylangLanguage = useCallback(() => {
		// Try to get language from URL parameter (admin bar language switcher)
		const urlParams = new URLSearchParams(window.location.search);
		const langParam = urlParams.get('lang');

		// If URL explicitly says 'all', return null (all languages)
		if (langParam === 'all') {
			return null;
		}

		// If URL has specific language, use it
		if (langParam && langParam !== 'all') {
			return langParam;
		}

		// DEFAULT: No URL parameter = show all languages
		// We DON'T check cookie because that would make language filter persistent
		// even when user navigates directly to page=ai-media-library
		return null;
	}, []);

	/**
	 * Build API URL with filters
	 * @param {number} page - Page number
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 * @returns {string} API URL
	 */
	const buildApiUrl = useCallback((page, perPage, filters) => {
		const params = new URLSearchParams({
			page: page,
			per_page: perPage,
			status: filters.status || 'all',
			attachment_filter: filters.attachmentFilter || 'all',
			search: filters.search || '',
		});

		// Add Polylang language filter
		const currentLang = detectPolylangLanguage();
		if (currentLang) {
			params.append('lang', currentLang);
		} else {
			// Explicitly add 'all' when no language is detected
			// This ensures backend shows all languages when no filter is active
			params.append('lang', 'all');
		}

		// Only add skip_count if TRUE (WordPress REST API expects 1/0 or true/false, not string 'false')
		// URLSearchParams converts boolean to string, so we use 1/0 instead
		if (filters.skip_count === true) {
			params.append('skip_count', '1');
		} else if (filters.skip_count === false) {
			params.append('skip_count', '0');
		}
		// If undefined, don't add the parameter (backend will default to false)

		return `/ai-media/v1/library?${params.toString()}`;
	}, [detectPolylangLanguage]);

	/**
	 * Generate cache key
	 * @param {number} page - Page number
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 * @returns {string} Cache key
	 */
	const getCacheKey = useCallback((page, perPage, filters) => {
		const skipCount = filters.skip_count ? 'skip' : 'count';
		const currentLang = detectPolylangLanguage() || 'all';
		return `page_${page}_${perPage}_${filters.status}_${filters.attachmentFilter}_${filters.search}_${skipCount}_${currentLang}`;
	}, [detectPolylangLanguage]);

	/**
	 * Load single page (with cache check)
	 * @param {number} page - Page number
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 * @returns {Promise<Object>} API response
	 */
	const loadPage = useCallback(async (page, perPage, filters) => {
		const cacheKey = getCacheKey(page, perPage, filters);

		// Check cache first
		if (cache.current.has(cacheKey)) {
			return cache.current.get(cacheKey);
		}

		// Fetch from API
		const url = buildApiUrl(page, perPage, filters);
		const response = await apiFetch({ path: url, method: 'GET' });

		// Store in cache
		cache.current.set(cacheKey, response);

		// Limit cache size (LRU - Least Recently Used)
		if (cache.current.size > MAX_CACHED_PAGES) {
			const firstKey = cache.current.keys().next().value;
			cache.current.delete(firstKey);
		}

		return response;
	}, [buildApiUrl, getCacheKey]);

	/**
	 * Prefetch adjacent pages in background with smart throttling
	 * Only prefetches the NEXT page (not previous or +2) to avoid blocking navigation
	 * Uses debouncing (1.5s delay) to wait until user stops navigating
	 * @param {number} currentPage - Current page number
	 * @param {number} totalPages - Total pages available
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 */
	const prefetchAdjacentPages = useCallback((currentPage, totalPages, perPage, filters) => {
		// Cancel any pending prefetch timeout
		if (prefetchTimeout.current) {
			clearTimeout(prefetchTimeout.current);
			prefetchTimeout.current = null;
		}

		// Cancel any ongoing prefetch request
		if (prefetchAbortController.current) {
			prefetchAbortController.current.abort();
			prefetchAbortController.current = null;
		}

		// Debounce: Wait 1.5s before starting prefetch
		// This ensures user has stopped navigating
		prefetchTimeout.current = setTimeout(() => {
			// Only prefetch NEXT page (not previous, to avoid wasting bandwidth)
			if (currentPage < totalPages) {
				const nextKey = getCacheKey(currentPage + 1, perPage, filters);

				// Check if already cached
				if (!cache.current.has(nextKey)) {
					// Create AbortController for this prefetch
					prefetchAbortController.current = new AbortController();

					// Load next page in background
					loadPage(currentPage + 1, perPage, filters)
						.then(() => {
							prefetchAbortController.current = null;
						})
						.catch(() => {
							prefetchAbortController.current = null;
						});
				}
			}
		}, 1500); // 1.5 second debounce
	}, [getCacheKey, loadPage]);

	/**
	 * Clear all cache (on filter change)
	 */
	const clearCache = useCallback(() => {
		cache.current.clear();
	}, []);

	/**
	 * Get current cache size
	 * @returns {number} Number of cached pages
	 */
	const getCacheSize = useCallback(() => {
		return cache.current.size;
	}, []);

	return {
		loadPage,
		prefetchAdjacentPages,
		clearCache,
		getCacheSize,
	};
};
