/**
 * Reusable hook for managing editable fields with type-specific handlers
 */

import { useState } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { stripHtml } from '../utils/formatters';

/**
 * Generic hook for handling editable field state with type-specific logic
 * @param {string} fieldType - Type of field: 'alt', 'caption', 'title', 'status'
 * @param {Object} deps - Dependencies
 * @param {Function} deps.refreshAttachment - Refresh attachment after save
 * @param {Function} deps.isAttachmentProcessing - Check if attachment is processing
 * @returns {Object} Editable field state and handlers
 */
export const useEditableField = (fieldType = null, deps = {}) => {
	const { refreshAttachment = null, isAttachmentProcessing = null } = deps;

	const [editing, setEditing] = useState(null); // {id, value, isDraft}
	const [saving, setSaving] = useState(false);

	/**
	 * Start editing a field
	 * @param {number} id - Item ID
	 * @param {string} value - Current value
	 * @param {boolean} isDraft - Whether this is a draft value
	 */
	const startEditing = (id, value, isDraft = false) => {
		setEditing({ id, value, isDraft });
	};

	/**
	 * Update editing value
	 * @param {string} value - New value
	 */
	const updateValue = (value) => {
		setEditing(prev => ({ ...prev, value }));
	};

	/**
	 * Cancel editing
	 */
	const cancelEditing = () => {
		setEditing(null);
	};

	/**
	 * Save editing (wrapper that sets saving state)
	 * @param {Function} saveFn - Async function to save the value
	 */
	const save = async (saveFn) => {
		if (!editing || saving) return;

		setSaving(true);
		try {
			await saveFn(editing);
			setEditing(null);
		} catch (err) {
			throw err; // Re-throw for caller to handle
		} finally {
			setSaving(false);
		}
	};

	/**
	 * Type-specific click handler
	 * @param {Object} attachment - Attachment object
	 */
	const handleClick = (attachment) => {
		if (!fieldType) return; // If no fieldType specified, use manual handlers
		if (isAttachmentProcessing && isAttachmentProcessing(attachment)) return;

		switch (fieldType) {
			case 'alt': {
				const isDraft = attachment.meta?._ai_media_status === 'processed' && attachment.meta?._ai_media_draft_alt;
				const currentValue = isDraft
					? attachment.meta._ai_media_draft_alt
					: attachment.alt_text || '';
				startEditing(attachment.id, currentValue, isDraft);
				break;
			}

			case 'caption': {
				const isDraft = attachment.meta?._ai_media_status === 'processed' && attachment.meta?._ai_media_draft_caption;
				let currentCaption = '';

				if (isDraft) {
					currentCaption = attachment.meta._ai_media_draft_caption;
				} else if (typeof attachment.caption === 'string') {
					currentCaption = stripHtml(attachment.caption);
				} else if (attachment.caption?.rendered) {
					currentCaption = stripHtml(attachment.caption.rendered);
				}
				startEditing(attachment.id, currentCaption, isDraft);
				break;
			}

			case 'title': {
				const isDraft = attachment.meta?._ai_media_status === 'processed' && attachment.meta?._ai_media_draft_title;
				let currentValue = '';

				if (isDraft) {
					currentValue = attachment.meta._ai_media_draft_title;
				} else if (typeof attachment.title === 'string') {
					currentValue = stripHtml(attachment.title);
				} else if (attachment.title?.rendered) {
					currentValue = stripHtml(attachment.title.rendered);
				}
				startEditing(attachment.id, currentValue, isDraft);
				break;
			}

			case 'status': {
				const currentStatus = attachment.meta?._ai_media_status || 'pending';
				startEditing(attachment.id, currentStatus, false);
				break;
			}

			default:
				break;
		}
	};

	/**
	 * Type-specific keydown handler
	 * @param {KeyboardEvent} e - Keyboard event
	 */
	const handleKeyDown = (e) => {
		if (!fieldType) return;

		if (e.key === 'Escape') {
			cancelEditing();
		} else if (e.key === 'Enter' && !e.shiftKey) {
			e.preventDefault();
			handleSave();
		}
	};

	/**
	 * Type-specific save handler
	 */
	const handleSave = async () => {
		if (!fieldType || !refreshAttachment) return;

		await save(async (editing) => {
			switch (fieldType) {
				case 'alt':
					await apiFetch({
						path: `/wp/v2/media/${editing.id}`,
						method: 'POST',
						data: { alt_text: editing.value },
					});

					if (editing.isDraft) {
						await apiFetch({
							path: `/wp/v2/media/${editing.id}`,
							method: 'POST',
							data: {
								meta: { _ai_media_draft_alt: '' },
							},
						});
					}
					break;

				case 'caption':
					await apiFetch({
						path: `/wp/v2/media/${editing.id}`,
						method: 'POST',
						data: { caption: editing.value },
					});

					if (editing.isDraft) {
						await apiFetch({
							path: `/wp/v2/media/${editing.id}`,
							method: 'POST',
							data: {
								meta: { _ai_media_draft_caption: '' },
							},
						});
					}
					break;

				case 'title':
					await apiFetch({
						path: `/wp/v2/media/${editing.id}`,
						method: 'POST',
						data: { title: editing.value },
					});

					if (editing.isDraft) {
						await apiFetch({
							path: `/wp/v2/media/${editing.id}`,
							method: 'POST',
							data: {
								meta: { _ai_media_draft_title: '' },
							},
						});
					}
					break;

				case 'status':
					// If changing to pending, clear metadata and set defaults from filename
					if (editing.value === 'pending') {
						// Fetch attachment to get filename
						const attachment = await apiFetch({
							path: `/wp/v2/media/${editing.id}`,
							method: 'GET',
						});

						// Get filename without extension
						let defaultName = attachment.title?.rendered || '';
						if (attachment.source_url) {
							const filename = attachment.source_url.split('/').pop();
							const nameWithoutExt = filename.replace(/\.[^/.]+$/, '');
							defaultName = nameWithoutExt.replace(/[-_]/g, ' ');
						}

						// Clear metadata and set to filename
						await apiFetch({
							path: `/wp/v2/media/${editing.id}`,
							method: 'POST',
							data: {
								alt_text: '',
								caption: '',
								title: defaultName,
								meta: {
									_ai_media_status: 'pending',
									_ai_media_draft_alt: '',
									_ai_media_draft_title: '',
									_ai_media_draft_caption: '',
								},
							},
						});
					} else {
						// Just update status
						await apiFetch({
							path: `/wp/v2/media/${editing.id}`,
							method: 'POST',
							data: {
								meta: { _ai_media_status: editing.value },
							},
						});
					}
					break;

				default:
					break;
			}

			await refreshAttachment(editing.id);
		});
	};

	return {
		editing,
		saving,
		startEditing,
		updateValue,
		cancelEditing,
		save,
		setSaving,
		// Type-specific handlers (only if fieldType is provided)
		...(fieldType && {
			handleClick,
			handleKeyDown,
			handleSave,
		}),
	};
};

/**
 * Hook for approve functionality
 * @param {Object} deps - Dependencies
 * @param {Function} deps.refreshAttachment - Refresh attachment after approve
 * @param {Function} deps.isAttachmentProcessing - Check if attachment is processing
 * @returns {Object} Approve handler
 */
export const useApprove = ({ refreshAttachment, isAttachmentProcessing }) => {
	/**
	 * Approve draft metadata or change status from processed to approved
	 * @param {Object} attachment - Attachment object
	 */
	const handleApprove = async (attachment) => {
		if (isAttachmentProcessing && isAttachmentProcessing(attachment)) return;

		try {
			const updateData = { meta: {} };

			// Approve draft alt text if exists
			if (attachment.meta?._ai_media_draft_alt) {
				updateData.alt_text = attachment.meta._ai_media_draft_alt;
				updateData.meta._ai_media_draft_alt = '';
			}

			// Approve draft title if exists
			if (attachment.meta?._ai_media_draft_title) {
				updateData.title = attachment.meta._ai_media_draft_title;
				updateData.meta._ai_media_draft_title = '';
			}

			// Approve draft caption if exists
			if (attachment.meta?._ai_media_draft_caption) {
				updateData.caption = attachment.meta._ai_media_draft_caption;
				updateData.meta._ai_media_draft_caption = '';
			}

			// Always change status to approved (even without draft changes)
			updateData.meta._ai_media_status = 'approved';

			await apiFetch({
				path: `/wp/v2/media/${attachment.id}`,
				method: 'POST',
				data: updateData,
			});

			if (refreshAttachment) {
				await refreshAttachment(attachment.id);
			}
		} catch (err) {
			alert('Error approving metadata: ' + err.message);
		}
	};

	return { handleApprove };
};
