/**
 * Hook for managing column visibility in the media library table
 */

import { useState, useEffect } from '@wordpress/element';

/**
 * Manage column visibility state and listen for changes from Screen Options
 * @returns {Object} Column visibility state and utilities
 */
export const useColumnVisibility = () => {
	const [hiddenColumns, setHiddenColumns] = useState(
		window.aiMediaSEO?.screenOptions?.hiddenColumns || []
	);

	// Listen for column visibility changes from Screen Options
	useEffect(() => {
		const handleColumnVisibilityChange = (event) => {
			setHiddenColumns(event.detail.hiddenColumns);
		};

		window.addEventListener('aiMediaColumnVisibilityChanged', handleColumnVisibilityChange);

		return () => {
			window.removeEventListener('aiMediaColumnVisibilityChanged', handleColumnVisibilityChange);
		};
	}, []);

	/**
	 * Check if a column is visible
	 * @param {string} columnName - Name of the column
	 * @returns {boolean} True if column is visible
	 */
	const isColumnVisible = (columnName) => {
		return !hiddenColumns.includes(columnName);
	};

	return {
		hiddenColumns,
		isColumnVisible,
	};
};
