/**
 * Thumbnail Cell
 * Displays attachment thumbnail with hover preview
 */

import { useState } from '@wordpress/element';

/**
 * ThumbnailCell component
 * @param {Object} props - Component props
 * @param {Object} props.attachment - Attachment object
 * @param {Function} props.onMouseEnter - Callback when mouse enters thumbnail
 * @param {Function} props.onMouseLeave - Callback when mouse leaves thumbnail
 * @returns {JSX.Element} Rendered thumbnail cell
 */
const ThumbnailCell = ({ attachment, onMouseEnter, onMouseLeave }) => {
	// Get thumbnail URL - fallback to source_url if thumbnail not available
	const thumbnailUrl = attachment.media_details?.sizes?.thumbnail?.source_url || attachment.source_url;
	const [imageError, setImageError] = useState(false);

	// Handle image load error (404, etc.)
	const handleImageError = () => {
		setImageError(true);
	};

	return (
		<td
			className="thumbnail-column"
			onMouseEnter={onMouseEnter}
			onMouseLeave={onMouseLeave}
		>
			<a
				href={`${window.aiMediaSEO?.adminUrl || '/wp-admin/'}post.php?post=${attachment.id}&action=edit`}
				target="_blank"
				rel="noopener noreferrer"
				style={{ display: 'block', cursor: 'pointer' }}
			>
				{thumbnailUrl && !imageError ? (
					<img
						src={thumbnailUrl}
						alt={attachment.alt_text || ''}
						style={{ width: '60px', height: '60px', objectFit: 'cover' }}
						onError={handleImageError}
					/>
				) : (
					<div
						className="placeholder-image"
						style={{
							width: '60px',
							height: '60px',
							background: '#ddd',
							display: 'flex',
							alignItems: 'center',
							justifyContent: 'center',
							color: '#999',
							fontSize: '10px',
							textAlign: 'center'
						}}
					>
						No Image
					</div>
				)}
			</a>
		</td>
	);
};

export default ThumbnailCell;
