/**
 * Hook for managing batch image processing with parallel processing support
 * @version 2.2.0 - Added speed modes and parallel processing
 */

import { useState, useRef } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { getProcessingStatusMessage } from '../utils/constants';

/**
 * Retry configuration per speed mode
 */
const getRetryConfig = (speedMode) => {
	switch (speedMode) {
		case 'easy':
			return {
				maxRetries: 1,      // Pouze 1 retry
				baseDelay: 2000,    // 2s base delay
				jitterMs: 500,      // 500ms jitter
			};
		case 'optimal':
			return {
				maxRetries: 2,      // 2 retry pokusy
				baseDelay: 1000,    // 1s base delay
				jitterMs: 300,      // 300ms jitter
			};
		case 'extreme':
			return {
				maxRetries: 3,      // 3 retry pokusy (agresivn)
				baseDelay: 500,     // 500ms base delay
				jitterMs: 200,      // 200ms jitter
			};
		default:
			return {
				maxRetries: 2,
				baseDelay: 1000,
				jitterMs: 300,
			};
	}
};

/**
 * Delay between chunks per speed mode
 */
const getDelayBetweenChunks = (speedMode) => {
	switch (speedMode) {
		case 'easy':
			return 500; // 500ms pauza pro extra stabilitu
		case 'optimal':
			return 200; // 200ms pauza
		case 'extreme':
			return 0; // dn pauza, maximln rychlost
		default:
			return 200;
	}
};

/**
 * Helper delay function
 */
const delay = (ms) => new Promise(resolve => setTimeout(resolve, ms));

/**
 * Hook for batch processing and single image analysis
 * @param {Object} deps - Dependencies from other hooks
 * @param {Function} deps.addProcessingId - From useQueueStatus
 * @param {Function} deps.removeProcessingId - From useQueueStatus
 * @param {Function} deps.refreshAttachment - From useAttachments
 * @param {Function} deps.addCost - From useCostTracking
 * @param {Array} deps.processingIds - From useQueueStatus
 * @param {Function} deps.setAllAttachments - From useAttachments
 * @param {Array} deps.selectedIds - From useSelection
 * @param {Function} deps.clearSelection - From useSelection
 * @param {Function} deps.setAdminNotice - From MediaLibrary
 * @returns {Object} Batch processing state and handlers
 */
export const useBatchProcessing = ({
	addProcessingId,
	removeProcessingId,
	refreshAttachment,
	addCost,
	processingIds,
	setAllAttachments,
	selectedIds,
	clearSelection,
	setAdminNotice,
}) => {
	const [batchProcessing, setBatchProcessing] = useState(false);
	const [batchProgress, setBatchProgress] = useState(null);
	const cancelledRef = useRef(false);
	const [processingStatus, setProcessingStatus] = useState('');
	const [speedMode, setSpeedMode] = useState('optimal');
	const [concurrency, setConcurrency] = useState(8);
	const retriedCountRef = useRef(0);

	/**
	 * Process single image with retry logic
	 * @param {number} attachmentId - Attachment ID to process
	 * @param {string} speedMode - Speed mode (easy|optimal|extreme)
	 * @returns {Promise<Object>} Result object
	 */
	const processImageWithRetry = async (attachmentId, speedMode) => {
		const retryConfig = getRetryConfig(speedMode);
		let lastError;

		for (let attempt = 0; attempt <= retryConfig.maxRetries; attempt++) {
			// Check for cancellation before each attempt
			if (cancelledRef.current) {
				throw new Error('Batch processing cancelled');
			}

			try {
				const result = await apiFetch({
					path: '/ai-media/v1/process-single',
					method: 'POST',
					data: {
						attachment_id: attachmentId,
						auto_apply: true,
						force_reprocess: true,
					},
				});

				return { success: true, result, attachmentId };

			} catch (error) {
				lastError = error;

				// If cancelled, don't retry
				if (cancelledRef.current) {
					throw new Error('Batch processing cancelled');
				}

				// Rozliit typ chyby
				const isRetryable =
					error.code === 'rate_limit_exceeded' ||
					error.code === 'timeout' ||
					error.status === 429 ||
					error.status === 503;

				if (!isRetryable || attempt >= retryConfig.maxRetries) {
					// Neretryovateln chyba nebo vyerpan pokusy
					throw error;
				}

				// Exponential backoff s jitterem
				const backoffDelay = retryConfig.baseDelay * Math.pow(2, attempt);
				const jitter = Math.random() * retryConfig.jitterMs;
				await delay(backoffDelay + jitter);

				retriedCountRef.current++;

				// Log retry attempt
				console.log(`Retrying image ${attachmentId}, attempt ${attempt + 1}/${retryConfig.maxRetries}`);
			}
		}

		throw lastError;
	};

	/**
	 * Process batch with parallel processing
	 * @param {Array<number>} imageIds - Image IDs to process
	 * @param {string} speedMode - Speed mode
	 * @param {number} concurrency - Concurrent requests
	 * @returns {Promise<Object>} Results summary
	 */
	const processBatchParallel = async (imageIds, speedMode, concurrency) => {
		const results = {
			processed: 0,
			success: 0,
			failed: 0,
			errors: {},
			retried: 0,
		};

		// Reset retry counter
		retriedCountRef.current = 0;

		// Rozdlen do chunk pro paraleln zpracovn
		for (let i = 0; i < imageIds.length; i += concurrency) {
			// Check for cancellation
			if (cancelledRef.current) break;

			const chunk = imageIds.slice(i, i + concurrency);

			// Zpracovat chunk paraleln s retry logikou
			const promises = chunk.map(imageId =>
				processImageWithRetry(imageId, speedMode)
					.then(({ result, attachmentId }) => {
						results.processed++;
						results.success++;

						// Track costs
						if (result.costs && result.costs.total_cost) {
							addCost(result.costs.total_cost);
						}

						// Get data from API response (now includes thumbnail_url, alt_text, provider)
						const thumbnailUrl = result.thumbnail_url || result.source_url || '';
						const altText = result.alt_text || 'Processing...';
						const provider = result.provider || 'unknown';

						// Update progress with enhanced data
						setBatchProgress(prev => ({
							...prev,
							current: results.processed,
							success: [...prev.success, imageId],
							retried: retriedCountRef.current,

							// NEW: Add to recent feed
							recentlyProcessed: [
								{
									id: attachmentId,
									thumbnail: thumbnailUrl,
									alt: altText,
									timestamp: Date.now(),
									provider: provider,
								},
								...prev.recentlyProcessed.slice(0, 14), // Keep last 15
							],

							// NEW: Update total cost
							totalCost: prev.totalCost + (result.costs?.total_cost || 0),

							// NEW: Update provider stats
							providerStats: {
								...prev.providerStats,
								[provider?.toLowerCase() || 'unknown']:
									(prev.providerStats[provider?.toLowerCase() || 'unknown'] || 0) + 1,
							},
						}));

						// Refresh attachment in UI
						refreshAttachment(imageId);

						return { imageId, success: true, result };
					})
					.catch(error => {
						results.processed++;
						results.failed++;
						results.errors[imageId] = [error.message || 'Unknown error'];

						// Update progress
						setBatchProgress(prev => ({
							...prev,
							current: results.processed,
							failed: [...prev.failed, imageId],
							errors: {
								...prev.errors,
								[imageId]: [error.message || 'Unknown error'],
							},
							retried: retriedCountRef.current,
						}));

						// Refresh attachment in UI
						refreshAttachment(imageId);

						return { imageId, success: false, error };
					})
			);

			// Pokat na dokonen celho chunku
			await Promise.allSettled(promises);

			// Adaptivn delay mezi chunky podle speed mode
			const delayMs = getDelayBetweenChunks(speedMode);
			if (i + concurrency < imageIds.length && delayMs > 0) {
				await delay(delayMs);
			}

			// Update processing status message
			setProcessingStatus(getProcessingStatusMessage());
		}

		return results;
	};

	/**
	 * Handle single image analysis
	 * @param {number} attachmentId - Attachment ID to analyze
	 */
	const handleAnalyze = async (attachmentId) => {
		if (processingIds.includes(attachmentId)) return;

		addProcessingId(attachmentId);

		// Immediately update attachment to show "Processing..." state
		setAllAttachments(prev =>
			prev.map(att => {
				if (att.id === attachmentId) {
					return {
						...att,
						meta: {
							...att.meta,
							_ai_media_status: 'processing'
						},
						alt_text: 'Processing...',
						title: { rendered: 'Processing...' },
						caption: { rendered: 'Processing...' },
						// Preserve parent attachment info
						parent_info: att.parent_info,
						parent: att.parent,
						post: att.post,
					};
				}
				return att;
			})
		);

		try {
			const analyzeResponse = await apiFetch({
				path: '/ai-media/v1/analyze',
				method: 'POST',
				data: {
					attachment_ids: [attachmentId],
					// Language is auto-detected from attachment (Polylang/WPML)
				},
			});

			const result = analyzeResponse.results?.[0];

			// Check for errors first
			if (analyzeResponse.errors && analyzeResponse.errors.length > 0) {
				const error = analyzeResponse.errors[0];
				const errorMessages = error.errors || ['Unknown error'];
				alert('Failed to analyze image:\n' + errorMessages.join('\n'));
				await refreshAttachment(attachmentId);
			} else if (result) {
				if (result.costs && result.costs.total_cost) {
					addCost(result.costs.total_cost);
				}

				await refreshAttachment(attachmentId, {
					provider: result.provider || null,
					model: result.model || null,
					score: result.score !== null && result.score !== undefined ? parseFloat(result.score) : null,
				});
			} else {
				await refreshAttachment(attachmentId);
			}
		} catch (err) {
			alert('Error: ' + err.message);
			await refreshAttachment(attachmentId);
		} finally {
			removeProcessingId(attachmentId);
		}
	};

	/**
	 * Handle batch analysis with parallel processing
	 * @param {string} mode - Mode: 'selected' or 'unprocessed'
	 * @param {string} selectedSpeedMode - Speed mode (easy|optimal|extreme)
	 * @param {number} selectedConcurrency - Concurrent requests
	 */
	const handleBatchAnalyze = async (mode, selectedSpeedMode = 'optimal', selectedConcurrency = 8) => {
		if (batchProcessing) return;

		try {
			setBatchProcessing(true);
			cancelledRef.current = false;
			setAdminNotice(null);
			setSpeedMode(selectedSpeedMode);
			setConcurrency(selectedConcurrency);

			const batchResponse = await apiFetch({
				path: '/ai-media/v1/batch-analyze',
				method: 'POST',
				data: {
					mode: mode,
					attachment_ids: mode === 'selected' ? selectedIds : [],
					// Language is auto-detected per attachment from Polylang/parent post
				},
			});

			if (!batchResponse.success || batchResponse.attachment_ids.length === 0) {
				setBatchProcessing(false);
				setAdminNotice({
					type: 'warning',
					message: batchResponse.message || 'No images found to process.',
				});
				return;
			}

			const attachmentIds = batchResponse.attachment_ids;

			setBatchProgress({
				active: true,
				current: 0,
				total: attachmentIds.length,
				success: [],
				failed: [],
				errors: {},
				retried: 0,
				speedMode: selectedSpeedMode,
				concurrency: selectedConcurrency,
				// NEW FIELDS for enhanced modal
				startTime: Date.now(),
				recentlyProcessed: [],
				totalCost: 0,
				messages: [],
				achievements: [],
				providerStats: {
					openai: 0,
					google: 0,
				},
			});

			// Use parallel processing
			await processBatchParallel(attachmentIds, selectedSpeedMode, selectedConcurrency);

			// Show completion notice
			setBatchProgress(prev => {
				const successCount = prev.success.length;
				const failedCount = prev.failed.length;
				const wasCancelled = cancelledRef.current;

				if (wasCancelled) {
					setAdminNotice({
						type: 'warning',
						message: `Batch processing cancelled. Processed ${prev.current} of ${prev.total} images (${successCount} successful, ${failedCount} failed, ${prev.retried} retried). Refreshing page...`,
					});
				} else {
					if (failedCount === 0) {
						setAdminNotice({
							type: 'success',
							message: `Batch processing completed successfully! ${successCount} images processed${prev.retried > 0 ? ` (${prev.retried} retried)` : ''}. Refreshing page...`,
						});
					} else if (successCount > 0) {
						setAdminNotice({
							type: 'warning',
							message: `Batch processing completed with some errors. ${successCount} successful, ${failedCount} failed${prev.retried > 0 ? ` (${prev.retried} retried)` : ''}. Refreshing page...`,
						});
					} else {
						setAdminNotice({
							type: 'error',
							message: `Batch processing failed. All ${failedCount} images failed to process.`,
						});
					}
				}

				return prev;
			});

			setBatchProgress({ active: false });
			setBatchProcessing(false);
			clearSelection();

			// Hard refresh page after 2 seconds to show fresh data
			setTimeout(() => {
				window.location.reload();
			}, 2000);

		} catch (err) {
			setAdminNotice({
				type: 'error',
				message: err.message || 'An error occurred during batch processing.',
			});
			setBatchProgress({ active: false });
		} finally {
			setBatchProcessing(false);
		}
	};

	return {
		batchProcessing,
		batchProgress,
		setBatchProgress,
		cancelledRef,
		processingStatus,
		speedMode,
		concurrency,
		handleAnalyze,
		handleBatchAnalyze,
	};
};
