/**
 * Hook for managing pagination state in the media library
 *
 * Simple state management for pagination. Server-side pagination is handled by the API endpoint.
 */

import { useState } from '@wordpress/element';
import { DEFAULTS } from '../utils/constants';

/**
 * Manage pagination state
 * @returns {Object} Pagination state and utilities
 */
export const usePagination = () => {
	const [currentPage, setCurrentPage] = useState(1);
	const [perPage, setPerPage] = useState(window.aiMediaSEO?.screenOptions?.perPage || DEFAULTS.PER_PAGE);

	/**
	 * Go to a specific page
	 * @param {number} page - Page number (1-indexed)
	 */
	const goToPage = (page) => {
		setCurrentPage(page);
		// Scroll to top of table
		document.querySelector('.ai-media-library-table')?.scrollIntoView({ behavior: 'smooth' });
	};

	/**
	 * Reset to first page
	 */
	const resetPage = () => {
		setCurrentPage(1);
	};

	return {
		currentPage,
		perPage,
		goToPage,
		resetPage,
		setCurrentPage,
		setPerPage,
	};
};
