/**
 * Batch Processing Modal Wrapper
 * Decides between simple and enhanced modal based on batch size
 * @version 3.0.0
 */
import SimpleBatchModal from './SimpleBatchModal';
import EnhancedBatchModal from './EnhancedBatchModal/EnhancedBatchModal';

const ENHANCED_THRESHOLD = 20; // Show enhanced UI for >20 images

/**
 * BatchProcessingModal component wrapper
 * @param {Object} props - Component props
 * @param {Object} props.batchProgress - Batch progress data
 * @param {string} props.processingStatus - Current processing status message
 * @param {Function} props.onCancel - Callback when user cancels
 * @param {Function} props.onClose - Callback to close modal when complete
 * @param {Object} props.cancelledRef - Ref to track if user cancelled
 * @returns {JSX.Element|null} Rendered modal or null if not active
 */
const BatchProcessingModal = ({
	batchProgress,
	processingStatus,
	onCancel,
	onClose,
	cancelledRef
}) => {
	if (!batchProgress?.active) {
		return null;
	}

	const shouldShowEnhanced = batchProgress.total > ENHANCED_THRESHOLD;

	if (shouldShowEnhanced) {
		return (
			<EnhancedBatchModal
				batchProgress={batchProgress}
				processingStatus={processingStatus}
				onCancel={onCancel}
				onClose={onClose}
				cancelledRef={cancelledRef}
			/>
		);
	}

	return (
		<SimpleBatchModal
			batchProgress={batchProgress}
			processingStatus={processingStatus}
			onCancel={onCancel}
			onClose={onClose}
			cancelledRef={cancelledRef}
		/>
	);
};

export default BatchProcessingModal;
