/**
 * Helper functions for working with attachments
 */

import { STATUS_VALUES } from './constants';
import { stripHtml } from './formatters';

/**
 * Check if attachment is currently being processed (cannot be edited)
 * @param {Object} attachment - Attachment object
 * @param {Object} queueStatus - Queue status map
 * @returns {boolean} True if attachment is being processed
 */
export const isAttachmentProcessing = (attachment, queueStatus = {}) => {
	const status = attachment.meta?._ai_media_status || STATUS_VALUES.PENDING;
	const inQueue = queueStatus[attachment.id]?.in_queue || false;

	// Processing only if:
	// 1. Status is 'processing' (actively being processed by Action Scheduler)
	// 2. Status is 'pending' AND in queue (waiting in Action Scheduler queue)
	// Old images with status 'pending' but NOT in queue are editable!
	return status === STATUS_VALUES.PROCESSING || (status === STATUS_VALUES.PENDING && inQueue);
};

/**
 * Check if attachment has draft metadata (status is 'processed')
 * @param {Object} attachment - Attachment object
 * @returns {boolean} True if attachment has draft metadata
 */
export const isDraftStatus = (attachment) => {
	return attachment.meta?._ai_media_status === STATUS_VALUES.PROCESSED;
};

/**
 * Get display ALT text (draft or actual)
 * @param {Object} attachment - Attachment object
 * @returns {string} ALT text to display
 */
export const getDisplayAlt = (attachment) => {
	const isDraft = isDraftStatus(attachment);
	const draftAlt = attachment.meta?._ai_media_draft_alt || '';
	const actualAlt = attachment.alt_text || '';

	return isDraft && draftAlt ? draftAlt : actualAlt;
};

/**
 * Get display caption (draft or actual)
 * @param {Object} attachment - Attachment object
 * @returns {string} Caption to display
 */
export const getDisplayCaption = (attachment) => {
	const isDraft = isDraftStatus(attachment);
	const draftCaption = attachment.meta?._ai_media_draft_caption || '';
	const actualCaption = attachment.caption?.rendered ? stripHtml(attachment.caption.rendered) : '';

	return isDraft && draftCaption ? draftCaption : actualCaption;
};

/**
 * Get display title (draft or actual)
 * @param {Object} attachment - Attachment object
 * @returns {string} Title to display
 */
export const getDisplayTitle = (attachment) => {
	const isDraft = isDraftStatus(attachment);
	const draftTitle = attachment.meta?._ai_media_draft_title || '';
	const actualTitle = attachment.title?.rendered ? stripHtml(attachment.title.rendered) : '';

	return isDraft && draftTitle ? draftTitle : actualTitle;
};

/**
 * Get display status (considering queue status)
 * @param {Object} attachment - Attachment object
 * @param {Object} queueStatus - Queue status map
 * @returns {string} Status to display
 */
export const getDisplayStatus = (attachment, queueStatus = {}) => {
	const inQueue = queueStatus[attachment.id]?.in_queue || false;

	if (inQueue) {
		return STATUS_VALUES.PROCESSING;
	}

	return attachment.meta?._ai_media_status || STATUS_VALUES.PENDING;
};

/**
 * Get display status label (capitalized)
 * @param {Object} attachment - Attachment object
 * @param {Object} queueStatus - Queue status map
 * @returns {string} Status label to display
 */
export const getDisplayStatusLabel = (attachment, queueStatus = {}) => {
	const status = getDisplayStatus(attachment, queueStatus);
	return status.charAt(0).toUpperCase() + status.slice(1);
};

/**
 * Check if attachment has a parent post
 * @param {Object} attachment - Attachment object
 * @returns {boolean} True if attached to a post
 */
export const hasParentPost = (attachment) => {
	const parentId = attachment.post || attachment.parent || 0;
	return parentId && parentId !== 0;
};

/**
 * Get parent post ID
 * @param {Object} attachment - Attachment object
 * @returns {number} Parent post ID or 0
 */
export const getParentPostId = (attachment) => {
	return attachment.post || attachment.parent || 0;
};
