/**
 * Hook for managing library data with server-side pagination
 *
 * Uses the new /ai-media/v1/library endpoint with built-in caching and prefetching.
 */

import { useState, useCallback, useRef } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import { useLibraryCache } from './useLibraryCache';
import { stripHtml } from '../utils/formatters';

/**
 * Manage library data with server-side operations
 * @returns {Object} Library state and utilities
 */
export const useAttachments = () => {
	const [items, setItems] = useState([]);
	const [totalItems, setTotalItems] = useState(null); // null = loading, number = loaded
	const [totalPages, setTotalPages] = useState(null); // null = loading, number = loaded
	const [loading, setLoading] = useState(true);
	const [countLoading, setCountLoading] = useState(false); // true = COUNT loading in background
	const [error, setError] = useState(null);

	const { loadPage, prefetchAdjacentPages, clearCache } = useLibraryCache();

	// AbortController for background COUNT requests (prevent race conditions)
	const countAbortController = useRef(null);

	/**
	 * PHASE 2: Load COUNT in background (progressive loading)
	 * @param {number} page - Current page number
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 */
	const loadCountInBackground = useCallback(async (page, perPage, filters) => {
		// SAFETY: Don't load count in background if filters are active
		// (count should already be loaded in TRADITIONAL MODE)
		const hasActiveFilters =
			filters.search ||
			(filters.status && filters.status !== 'all') ||
			(filters.attachmentFilter && filters.attachmentFilter !== 'all');

		if (hasActiveFilters) {
			console.log('Skipping background count - filters active, count already loaded');
			setCountLoading(false);
			return;
		}

		// Cancel previous COUNT request if still running (prevent race conditions)
		if (countAbortController.current) {
			countAbortController.current.abort();
		}

		// Create new AbortController for this request
		countAbortController.current = new AbortController();

		try {
			setCountLoading(true);

			// New endpoint - ONLY COUNT
			const params = new URLSearchParams({
				page: page,
				per_page: perPage,
				status: filters.status || 'all',
				attachment_filter: filters.attachmentFilter || 'all',
				search: filters.search || '',
			});

			const response = await apiFetch({
				path: `/ai-media/v1/library/count?${params}`,
				method: 'GET',
				signal: countAbortController.current.signal
			});

			// DEBUG: Log count response
			console.group(' Background COUNT loaded');
			console.log('Search:', filters.search || '(none)');
			console.log('Status:', filters.status || 'all');
			console.log('Attachment filter:', filters.attachmentFilter || 'all');
			console.log('Total items:', response.total);
			console.log('Total pages:', response.total_pages);
			console.groupEnd();

			// Update pagination (user is already working)
			setTotalItems(response.total);
			setTotalPages(response.total_pages);
			setCountLoading(false);

			// Clear the controller reference
			countAbortController.current = null;

			// PHASE 3: Prefetch adjacent pages (ENABLED with smart throttling)
			// Now uses debouncing (1.5s) and only prefetches next page
			prefetchAdjacentPages(page, response.total_pages, perPage, filters);

		} catch (err) {
			// Ignore abort errors (expected when new request starts)
			if (err.name === 'AbortError') {
				return;
			}

			setCountLoading(false);
			countAbortController.current = null;
		}
	}, [prefetchAdjacentPages]);

	/**
	 * Load library with progressive loading strategy
	 * @param {number} page - Page number
	 * @param {number} perPage - Items per page
	 * @param {Object} filters - Filter parameters
	 */
	const loadLibrary = useCallback(async (page, perPage, filters) => {
		// DEBUG: Log filter state
		console.group(' loadLibrary called');
		console.log('Page:', page);
		console.log('Per page:', perPage);
		console.log('Filters:', filters);

		// Determine if we should skip COUNT
		// Skip COUNT ONLY if:
		// 1. Not explicitly disabled (skip_count !== false)
		// 2. No active search
		// 3. No active status filter
		// 4. No active attachmentFilter
		const hasActiveFilters =
			filters.search ||                           // Search query
			(filters.status && filters.status !== 'all') ||  // Status filter
			(filters.attachmentFilter && filters.attachmentFilter !== 'all');

		const shouldSkipCount = filters.skip_count !== false && !hasActiveFilters;

		console.log('Has active filters:', hasActiveFilters);
		console.log('Should skip count:', shouldSkipCount);
		console.log('Mode:', shouldSkipCount ? 'PROGRESSIVE' : 'TRADITIONAL');
		console.groupEnd();

		if (shouldSkipCount) {
			// PROGRESSIVE MODE: Fast load without COUNT
			// SAFETY CHECK: Re-verify no active filters (paranoid check)
			const hasActiveFilters =
				filters.search ||
				(filters.status && filters.status !== 'all') ||
				(filters.attachmentFilter && filters.attachmentFilter !== 'all');

			if (hasActiveFilters) {
				// ABORT: Active filters detected, use TRADITIONAL MODE instead
				console.warn('Active filters detected in PROGRESSIVE MODE, switching to TRADITIONAL MODE');
				// Recursive call with skip_count=false
				return loadLibrary(page, perPage, { ...filters, skip_count: false });
			}

			try {
				setLoading(true);
				setError(null);

				// skip_count=true  backend skips COUNT query
				const response = await loadPage(page, perPage, {
					...filters,
					skip_count: true
				});

				// Display data immediately (without total)
				setItems(response.items);
				setTotalItems(null); // don't know yet
				setTotalPages(null);
				setLoading(false);

				// Start PHASE 2 in background (only if no filters)
				loadCountInBackground(page, perPage, filters);

			} catch (err) {
				setError(err.message);
				setLoading(false);
			}
		} else {
			// TRADITIONAL MODE: Load with COUNT (for search/filters)
			try {
				setLoading(true);
				setError(null);

				// skip_count=false  backend includes COUNT
				const response = await loadPage(page, perPage, {
					...filters,
					skip_count: false
				});

				// Update state with COUNT
				setItems(response.items);
				setTotalItems(response.total);
				setTotalPages(response.total_pages);

				// Prefetch adjacent pages in background (ENABLED with smart throttling)
				if (response.total_pages) {
					prefetchAdjacentPages(page, response.total_pages, perPage, filters);
				}

				setLoading(false);
			} catch (err) {
				setError(err.message);
				setLoading(false);
			}
		}
	}, [loadPage, loadCountInBackground, prefetchAdjacentPages]);

	/**
	 * Refresh single attachment after update
	 * @param {number} attachmentId - Attachment ID to refresh
	 * @param {Object} jobData - Optional job data (provider, model, score)
	 */
	const refreshAttachment = useCallback(async (attachmentId, jobData = null) => {
		try {
			const response = await apiFetch({
				path: `/wp/v2/media/${attachmentId}`,
				method: 'GET',
			});

			// Fetch job data if not provided
			if (!jobData) {
				jobData = { provider: null, model: null, score: null };
				try {
					const jobsResponse = await apiFetch({
						path: `/ai-media/v1/jobs?attachment_ids=${attachmentId}`,
						method: 'GET',
					});

					const latestJob = jobsResponse.jobs
						?.filter(job => job.attachment_id == attachmentId)
						.sort((a, b) => new Date(b.created_at) - new Date(a.created_at))[0];

					if (latestJob) {
						jobData = {
							provider: latestJob.provider || null,
							model: latestJob.model || null,
							score: latestJob.score !== null ? parseFloat(latestJob.score) : null,
						};
					}
				} catch (jobErr) {
					// Failed to load job data, continue without it
				}
			}

			// Update in current items (optimistic update)
			setItems(prev =>
				prev.map(item => {
					if (item.id === attachmentId) {
						return {
							...response,
							provider: jobData.provider,
							model: jobData.model,
							score: jobData.score,
							// Preserve parent attachment info from previous state
							parent_info: item.parent_info,
							parent: item.parent,
							post: item.post,
							// Preserve language info from previous state (contains flag emoji)
							language_info: item.language_info,
						};
					}
					return item;
				})
			);

			// Note: We don't clear cache here - optimistic update is enough
			// Cache will be invalidated on page change or filter change

		} catch (err) {
			// Error refreshing attachment, silently fail
		}
	}, []);

	/**
	 * Attach media to a parent post/page
	 * @param {number} attachmentId - Attachment ID
	 * @param {number} parentId - Parent post/page ID
	 */
	const attachToParent = useCallback(async (attachmentId, parentId) => {
		await apiFetch({
			path: `/wp/v2/media/${attachmentId}`,
			method: 'POST',
			data: {
				post: parentId,
			},
		});

		// Fetch parent post details
		let parentPostData = null;
		try {
			const response = await apiFetch({
				path: `/wp/v2/posts/${parentId}`,
				method: 'GET',
			});

			parentPostData = {
				id: response.id,
				title: stripHtml(response.title?.rendered) || 'Untitled',
				type: response.type || 'post',
			};
		} catch (err) {
			// Try as a page
			try {
				const response = await apiFetch({
					path: `/wp/v2/pages/${parentId}`,
					method: 'GET',
				});

				parentPostData = {
					id: response.id,
					title: stripHtml(response.title?.rendered) || 'Untitled',
					type: response.type || 'page',
				};
			} catch (pageErr) {
				// Fallback
				parentPostData = {
					id: parentId,
					title: '(Unknown)',
					type: 'unknown',
				};
			}
		}

		// Update local state (optimistic update)
		setItems(prev =>
			prev.map(item => item.id === attachmentId ? {
				...item,
				post: parentId,
				parent: parentId,
				parent_info: parentPostData,
			} : item)
		);
	}, []);

	/**
	 * Detach media from its parent post/page
	 * @param {number} attachmentId - Attachment ID
	 */
	const handleDetach = useCallback(async (attachmentId) => {
		if (!confirm('Detach this image from its post/page?')) {
			return;
		}

		try {
			await apiFetch({
				path: `/wp/v2/media/${attachmentId}`,
				method: 'POST',
				data: {
					post: 0,
				},
			});

			// Update local state (optimistic update)
			setItems(prev =>
				prev.map(item => item.id === attachmentId ? {
					...item,
					post: 0,
					parent: 0,
					parent_info: null,
				} : item)
			);
		} catch (err) {
			alert('Error detaching image: ' + err.message);
		}
	}, []);

	/**
	 * Invalidate cache (call when filters change)
	 */
	const invalidateCache = useCallback(() => {
		clearCache();
	}, [clearCache]);

	return {
		// Data
		items,
		totalItems,    // null = loading, number = loaded
		totalPages,    // null = loading, number = loaded
		loading,       // true = PHASE 1 loading
		countLoading,  // true = PHASE 2 loading (background)
		error,

		// Functions
		loadLibrary,
		refreshAttachment,
		attachToParent,
		handleDetach,
		invalidateCache,

		// For compatibility (if other hooks use these)
		setItems,
		setLoading,
		setError,
	};
};
