/**
 * Hook for managing item selection in the media library
 */

import { useState } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';

/**
 * Manage selection state and provide selection utilities
 * @param {Function} loadAllAttachments - Reload attachments after delete (optional)
 * @returns {Object} Selection state and utilities
 */
export const useSelection = (loadAllAttachments = null) => {
	const [selectedIds, setSelectedIds] = useState([]);

	/**
	 * Toggle selection of a single item
	 * @param {number} id - Attachment ID
	 */
	const toggleSelection = (id) => {
		setSelectedIds(prev =>
			prev.includes(id)
				? prev.filter(selectedId => selectedId !== id)
				: [...prev, id]
		);
	};

	/**
	 * Select or deselect all items in a list
	 * @param {Array} items - Array of items (with id property)
	 */
	const selectAll = (items) => {
		// Get IDs of currently displayed (paginated) attachments
		const paginatedIds = items.map(att => att.id);

		// Check if all paginated items are already selected
		const allPaginatedSelected = paginatedIds.every(id => selectedIds.includes(id));

		if (allPaginatedSelected) {
			// Deselect all paginated items
			setSelectedIds(prev => prev.filter(id => !paginatedIds.includes(id)));
		} else {
			// Select all paginated items (add them to existing selection)
			setSelectedIds(prev => {
				const newIds = paginatedIds.filter(id => !prev.includes(id));
				return [...prev, ...newIds];
			});
		}
	};

	/**
	 * Clear all selection
	 */
	const clearSelection = () => {
		setSelectedIds([]);
	};

	/**
	 * Check if an item is selected
	 * @param {number} id - Attachment ID
	 * @returns {boolean} True if selected
	 */
	const isSelected = (id) => {
		return selectedIds.includes(id);
	};

	/**
	 * Bulk delete selected attachments
	 */
	const handleBulkDelete = async () => {
		if (selectedIds.length === 0) {
			alert('Please select images to delete.');
			return;
		}

		if (!confirm(`Delete ${selectedIds.length} selected images? This cannot be undone.`)) {
			return;
		}

		try {
			for (const id of selectedIds) {
				await apiFetch({
					path: `/wp/v2/media/${id}`,
					method: 'DELETE',
					data: { force: true },
				});
			}

			clearSelection();
			if (loadAllAttachments) {
				await loadAllAttachments();
			}
		} catch (err) {
			alert('Error deleting images: ' + err.message);
		}
	};

	/**
	 * Bulk change status for selected attachments
	 * @param {string} newStatus - New status (pending, processed, approved)
	 */
	const handleBulkStatusChange = async (newStatus) => {
		if (selectedIds.length === 0) {
			alert('Please select images to change status.');
			return;
		}

		const statusLabels = {
			pending: 'Pending',
			processed: 'Processed',
			approved: 'Approved',
		};

		if (!confirm(`Change status of ${selectedIds.length} selected images to "${statusLabels[newStatus]}"?`)) {
			return;
		}

		try {
			const response = await apiFetch({
				path: '/ai-media/v1/bulk-status',
				method: 'POST',
				data: {
					attachment_ids: selectedIds,
					status: newStatus,
				},
			});

			if (response.success) {
				// Clear selection and refresh page
				clearSelection();
				window.location.reload();
			} else {
				alert('Error changing status: ' + (response.message || 'Unknown error'));
			}
		} catch (err) {
			alert('Error changing status: ' + err.message);
		}
	};

	return {
		selectedIds,
		toggleSelection,
		selectAll,
		clearSelection,
		isSelected,
		handleBulkDelete,
		handleBulkStatusChange,
	};
};
