/**
 * Bulk Actions Component
 * Bulk actions toolbar with analyze and delete buttons, plus cost display
 */

import { useState } from '@wordpress/element';
import { Button } from '@wordpress/components';

/**
 * BulkActions component
 * @param {Object} props - Component props
 * @param {Array} props.selectedIds - Array of selected attachment IDs
 * @param {boolean} props.batchProcessing - Whether batch processing is in progress
 * @param {Function} props.onBatchAnalyze - Callback for batch analyze with mode ('selected', 'missing_metadata', 'all')
 * @param {Function} props.onBulkDelete - Callback for bulk delete
 * @param {Function} props.onBulkStatusChange - Callback for bulk status change
 * @param {number} props.totalCost - Total cost
 * @param {boolean} props.loadingCosts - Whether costs are loading
 * @param {Function} props.onShowCostModal - Callback to show cost modal
 * @param {Function} props.onLoadCostStats - Callback to load cost stats
 * @param {Function} props.onShowSettingsModal - Callback to show settings modal
 * @param {Function} props.onClearOpcache - Callback to clear OPCache
 * @returns {JSX.Element} Rendered bulk actions toolbar
 */
const BulkActions = ({
	selectedIds,
	batchProcessing,
	onBatchAnalyze,
	onBulkDelete,
	onBulkStatusChange,
	totalCost,
	loadingCosts,
	onShowCostModal,
	onLoadCostStats,
	onShowSettingsModal,
	onClearOpcache,
}) => {
	const [selectedStatus, setSelectedStatus] = useState('');
	return (
		<div className="ai-media-library-bulk-actions">
			{selectedIds.length > 0 ? (
				<>
					<Button
						variant="primary"
						onClick={() => onBatchAnalyze('selected')}
						disabled={batchProcessing}
						isBusy={batchProcessing}
					>
						{batchProcessing ? 'Processing...' : `Analyze Selected (${selectedIds.length})`}
					</Button>
					<Button
						variant="secondary"
						onClick={onBulkDelete}
						disabled={batchProcessing}
						style={{
							color: '#b32d2e',
							borderColor: '#b32d2e',
							marginLeft: '8px'
						}}
					>
						Bulk Delete ({selectedIds.length})
					</Button>

					{/* Status Change Controls */}
					<div style={{
						display: 'inline-flex',
						alignItems: 'center',
						gap: '8px',
						marginLeft: '16px',
						padding: '0 16px',
						borderLeft: '1px solid #dcdcde'
					}}>
						<label
							htmlFor="bulk-status-select"
							style={{
								fontSize: '13px',
								fontWeight: '500',
								color: '#50575e'
							}}
						>
							Change Status:
						</label>
						<select
							id="bulk-status-select"
							value={selectedStatus}
							onChange={(e) => setSelectedStatus(e.target.value)}
							disabled={batchProcessing}
							style={{
								padding: '6px 8px',
								border: '1px solid #8c8f94',
								borderRadius: '2px',
								fontSize: '13px',
								minWidth: '120px'
							}}
						>
							<option value="">-- Select Status --</option>
							<option value="pending">Pending (Delete all meta data)</option>
							<option value="processed">Processed</option>
							<option value="approved">Approved</option>
						</select>
						<Button
							variant={selectedStatus ? "primary" : "secondary"}
							onClick={() => onBulkStatusChange(selectedStatus)}
							disabled={batchProcessing || !selectedStatus}
							style={{
								fontSize: '13px'
							}}
						>
							Apply ({selectedIds.length})
						</Button>
					</div>
				</>
			) : (
				<>
					<Button
						variant="secondary"
						onClick={() => onBatchAnalyze('missing_metadata')}
						disabled={batchProcessing}
					>
						Analyze Missing Metadata
					</Button>
					<Button
						variant="secondary"
						onClick={() => onBatchAnalyze('all')}
						disabled={batchProcessing}
					>
						Analyze All Images
					</Button>
				</>
			)}

			{/* Clear OPCache Button */}
			<button
				type="button"
				onClick={onClearOpcache}
				style={{
					marginLeft: 'auto',
					padding: '10px 12px',
					background: '#f0f6fc',
					border: '1px solid #2271b1',
					borderRadius: '4px',
					display: 'flex',
					alignItems: 'center',
					justifyContent: 'center',
					cursor: 'pointer',
					transition: 'all 0.2s ease',
				}}
				onMouseEnter={(e) => {
					e.currentTarget.style.background = '#e5f2fa';
					e.currentTarget.style.borderColor = '#135e96';
				}}
				onMouseLeave={(e) => {
					e.currentTarget.style.background = '#f0f6fc';
					e.currentTarget.style.borderColor = '#2271b1';
				}}
				title="Clear OPCache"
			>
				<span
					className="dashicons dashicons-update"
					style={{
						color: '#2271b1',
						fontSize: '30px',
						width: '30px',
						height: '30px',
					}}
				></span>
			</button>

			{/* Settings Button */}
			<button
				type="button"
				onClick={onShowSettingsModal}
				style={{
					marginLeft: '8px',
					padding: '10px 12px',
					background: '#f0f6fc',
					border: '1px solid #2271b1',
					borderRadius: '4px',
					display: 'flex',
					alignItems: 'center',
					justifyContent: 'center',
					cursor: 'pointer',
					transition: 'all 0.2s ease',
				}}
				onMouseEnter={(e) => {
					e.currentTarget.style.background = '#e5f2fa';
					e.currentTarget.style.borderColor = '#135e96';
				}}
				onMouseLeave={(e) => {
					e.currentTarget.style.background = '#f0f6fc';
					e.currentTarget.style.borderColor = '#2271b1';
				}}
				title="Settings"
			>
				<span
					className="dashicons dashicons-admin-generic"
					style={{
						color: '#2271b1',
						fontSize: '30px',
						width: '30px',
						height: '30px',
					}}
				></span>
			</button>

			{/* Cost Display */}
			<button
				type="button"
				onClick={() => {
					onLoadCostStats();
					onShowCostModal();
				}}
				style={{
					marginLeft: '8px',
					padding: '8px 16px',
					background: '#f0f6fc',
					border: '1px solid #2271b1',
					borderRadius: '4px',
					display: 'flex',
					alignItems: 'center',
					gap: '8px',
					cursor: 'pointer',
					transition: 'all 0.2s ease',
				}}
				onMouseEnter={(e) => {
					e.currentTarget.style.background = '#e5f2fa';
					e.currentTarget.style.borderColor = '#135e96';
				}}
				onMouseLeave={(e) => {
					e.currentTarget.style.background = '#f0f6fc';
					e.currentTarget.style.borderColor = '#2271b1';
				}}
			>
				<span
					className="dashicons dashicons-money-alt"
					style={{
						color: '#2271b1',
						fontSize: '18px',
						width: '18px',
						height: '18px',
					}}
				></span>
				<div style={{ display: 'flex', flexDirection: 'column', alignItems: 'flex-end' }}>
					<span
						style={{
							fontSize: '11px',
							color: '#646970',
							fontWeight: '500',
							textTransform: 'uppercase',
							letterSpacing: '0.5px',
							lineHeight: '1.2',
						}}
					>
						Total Cost
					</span>
					<span
						style={{
							fontSize: '16px',
							fontWeight: '600',
							color: '#1d2327',
							lineHeight: '1.2',
						}}
					>
						{loadingCosts ? '...' : `$${totalCost.toFixed(4)}`}
					</span>
				</div>
			</button>
		</div>
	);
};

export default BulkActions;
